
import React, { useContext } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import Hidden from '@mui/material/Hidden'
import BannerLeftLogin from './components/bannerLeftLogin'
import { AuthContext } from '../../../context/general'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import FrontOfficeTheme from '../../../style/globalTheme'

import { useStyles, NewValidationTextField, StyledSimpleButton, StyledButtonFilled } from '../../style/modalsStyle'


import { ERROR_MESSAGE, PASSWORD, DIGITS, NAME, EMAIL, STAGE_SUBSCRIPTION } from '../../subscriptionModal/general'
import { STAGE_LOGIN, PAGE_CONFIRMATION } from '../../newLoginModal/general'


import DigitsInput from './components/digitsInput'
import BannerLeft from './components/bannerLeft'
import VerticalContainer from './components/verticalContainer'

import Section from './components/section'
import DisplayTests from './components/displayTests'
import RecapSection from './components/recapSection'

import { DisplayError, testPassDigits, testPassword } from '../../helpers/general'




import {
    useWindowSize,

} from '@react-hook/window-size'

import { initialState } from '../../subscriptionModal/general'
import widthFormula from '../helpers/computing'





const SubscriptionHomePage = (props) => {




    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <StyledEngineProvider injectFirst>
                <ThemeProvider theme={FrontOfficeTheme}>
                    <MainContainer props={props} />
                </ThemeProvider>
            </StyledEngineProvider>
        </ModalContainer>
    );

}

export default SubscriptionHomePage

const MainContainer = ({ props }) => {

    const { localState } = props

    const { authState } = useContext(AuthContext)
    const { currentProcess } = props



    const eventData = authState.showSubscriptionModal

    const [width, height] = useWindowSize()
    void height
    void width


    return (
        <Box display="flex" flexDirection="row" alignItems="stretch" justifyItems="stretch" width={widthFormula(width)}>
            <Hidden smDown>


            <VerticalContainer width="200px" margin="0 20px 0 0px">

                    <Section RECAP_LARGE height="500px" bgcolor="black">
                        {localState[NAME] === 'LOGIN' && <BannerLeftLogin props={props} />}
                        {localState[NAME] === 'SUBSCRIPTION' && <BannerLeft props={props} eventData={eventData} />}
                    </Section>


                </VerticalContainer >
            </Hidden>

            <VerticalContainer>
                      <Hidden smUp>
                    <Section flexShrink={2}>
                        < RecapSection toplabel="Confirmation" props={props} eventData={eventData} closeHandler={currentProcess?.closeHandler} />
                    </Section>

                </Hidden>

                <Section bgcolor="white" flex={50}>
                    <Box width="100%">
                        <Box width="100%" borderRadius="10px" bgcolor="white" padding="20px" >
                            < MiddleSectionContent props={props} />
                            <BottomSectionContent props={props} />
                        </Box>


                    </Box>
                </Section>





            </VerticalContainer>

        </Box>
    );
}










const MiddleSectionContent = ({ props }) => {

    const classes = useStyles()

    if (!props)
        return null

    const { localState, setLocalState } = props

    const onChange = (category) => (event) => {

        setLocalState({ ...localState, [category]: event.target.value })
    }






    const notConfirmed = (localState[STAGE_SUBSCRIPTION] === PAGE_CONFIRMATION + 'B') || (localState[STAGE_LOGIN] === PAGE_CONFIRMATION + 'B')
    const showSecondTextArea = props.currentProcess.name !=  'PAGE_CONFIRMATIONB'


    return (<Box display="flex" width="100%"  flexDirection="column" justifyContent="space-between" alignItems="stretch">
        <Box width="100%"  display="flex" flexDirection="column" alignItems="stretch">
            <Box >


                {!showSecondTextArea && <Typography style={{fontSize: '0.9rem'}}>
                    {"Renseignez le code  6 chiffres que vous avez reu sur votre boite mail"}{" "}{localState[EMAIL]}
                </Typography>}
                {showSecondTextArea && <Typography style={{fontSize: '0.8rem'}}>
                    {`Renseignez le code  6 chiffres que vous avez reu sur votre boite mail ${localState[EMAIL]} et choisissez un nouveau mot de passe`}
                </Typography>}
            </Box>

        </Box>
        
        <DigitsInput digit={localState[DIGITS]} setDigit={(x) => setLocalState({ ...localState, [DIGITS]: x })} />

        <Box marginTop="10px">
          
            {showSecondTextArea && <Box marginTop="10px" display="flex" width="100%" flexDirection="row" justifyContent="center" justifyItems="center">


                <NewValidationTextField

                    name="current-password"
                    id="current-password"
                    type="password"
                    label="mot de passe"
                    autoComplete="section-blue current-password"
                    InputProps={{
                        className: classes.input,
                        autoFocus: localState[STAGE_SUBSCRIPTION] === 'STAGE_0B' ? true : false
                    }}
                    InputLabelProps={{
                        shrink: true
                    }}
                    variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />
            </Box>}
            <Box margin="auto" marginTop="20px" width="300px" display="flex" flexDirection="row" justifyContent="center">
                {showSecondTextArea && <DisplayTests password={localState[PASSWORD]} />}

            </Box>


        </Box>
        <DisplayError message={localState[ERROR_MESSAGE]} />

    </Box>)
}

const BottomSectionContent = ({ props }) => {

    const classes = useStyles()

    console.log('@PROCESS')
    console.log(props)

    if (!props)
        return null

    const { localState, setLocalState, currentProcess } = props

    const notConfirmed = localState.STAGE_SUBSCRIPTION == 'PAGE_CONFIRMATIONB' || currentProcess.name == 'PAGE_CONFIRMATIONB'
   
    const clickHandler = () => {
       
        if (!testPassDigits(localState.DIGITS))
            setLocalState({ ...localState, [ERROR_MESSAGE]: 'Nombre de chiffres insuffisants' })
        else if (notConfirmed && !testPassword(localState[PASSWORD]))
            setLocalState({ ...localState, [ERROR_MESSAGE]: "Format de mot de passe incorrect" })
        else {

            console.log('@PAGE EMAIL CONF')
            console.log(props)
         
            if (notConfirmed){
                console.log('@PATH-1')
                currentProcess.page.workflowLogic.onClickMainButton(props)
            }
                
            else{
                console.log('@PATH-2')
                currentProcess.workflowLogic.onClickMainButton(props)
            }
               


        }


    }



    return (<Box>
        <StyledButtonFilled
            onClick={clickHandler}>
            <Typography className={classes.buttonTypography}>
                {"envoyer".toUpperCase()}
            </Typography>

        </StyledButtonFilled>
        {(currentProcess.name != 'PAGE_CONFIRMATIONA') && <Box marginTop="20px" textAlign="center">


            <StyledSimpleButton
                onClick={() => currentProcess.page.workflowLogic.onClickButton(props)}>
                <Typography  style={{ fontSize: '0.8rem', textDecoration: 'none', }}>
                    Obtenir un nouveau code
            </Typography>

            </StyledSimpleButton>   
        </Box>}

    </Box>)
}



