import { ContactSupportOutlined } from "@mui/icons-material"
import { API, graphqlOperation } from "aws-amplify"
import { createTransaction as GRAPHQL_createTransaction} from '../graphql/mutations'
import { transactionsByClientId} from '../graphql/queries'



const DBAPI = (props) => {

    const getTransactionsByClient = (props) => {
        const { clientId, callback, onError } = props

        API.graphql(graphqlOperation(transactionsByClientId, {clientId: clientId, limit: 100000000 }))
        .then(({data})=>{
            const transactions = data.transactionsByClientId.items
            const transactionsIds = transactions.map(transaction=>transaction.id)
            callback(transactionsIds)
        })
        .catch(err=>{
            if (onError)
            onError(err)
            else console.log(err)
        })

    }

        const createTransaction = (props) => {
            const { data, callback, onError } = props


       

   
        
    
        API.graphql(graphqlOperation(GRAPHQL_createTransaction, {input: data}))
            .then(({data}) => {  
                
               

          

                const transactionDetails = data.createTransaction

                console.log('@TRANSACTION CREATED IN DDB')
                console.log(transactionDetails)

            
               
                callback(transactionDetails)
            })
            .catch((err) => onError(err))  
    }

      return {
            createTransaction: () => createTransaction(props),
            getTransactionsByClient: () => getTransactionsByClient(props)
        }
        

}

export default DBAPI