
import React, { useContext } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import { DisplayError } from '../../helpers/general'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import FrontOfficeTheme from '../../../style/globalTheme'


import Hidden from '@mui/material/Hidden'

import { useStyles, StyledButton, StyledButtonFilled, NewValidationTextField, StyledUnderlinedButton } from '../../style/modalsStyle'

import { EMAIL, ERROR_MESSAGE, PASSWORD, NAME, STAGE_SUBSCRIPTION, initialState } from '../../subscriptionModal/general'

import { AuthContext } from '../../../context/general'

import BannerLeft from './components/bannerLeft'
import BannerLeftLogin from './components/bannerLeftLogin'
import VerticalContainer from './components/verticalContainer'

import Section from './components/section'
import RecapSection from './components/recapSection'



import {
    useWindowSize,

} from '@react-hook/window-size'







const SubscriptionHomePage = (props) => {

    const { currentProcess, setLocalState } = props




    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <StyledEngineProvider injectFirst>
                <ThemeProvider theme={FrontOfficeTheme}>
                    <MainContainer props={props} />
                </ThemeProvider>
            </StyledEngineProvider>
        </ModalContainer>
    );

}

export default SubscriptionHomePage

const MainContainer = ({ props }) => {

    const { localState , currentProcess, isLogged} = props

    React.useEffect(()=>{
        if (isLogged)
        //console.log('@ISLOOGED')
        currentProcess.workflowLogic.onClickMainButton({...props, test: isLogged})
      
       
    },[isLogged, currentProcess.workflowLogic, props])


    

    const { authState } = useContext(AuthContext)



    const eventData = authState.showSubscriptionModal
 




    const [width, height] = useWindowSize()
    void height
    void width

   


    return (
        <Box display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="100%">
            <Hidden smDown>
                <VerticalContainer width="200px" margin="0 20px 0 0px">
                    

                    <Section RECAP_LARGE bgcolor="black" height="100%" >
                        {localState[NAME] === 'LOGIN' && <BannerLeftLogin props={props} />}
                        {localState[NAME] === 'SUBSCRIPTION' && <BannerLeft props={props} eventData={eventData} />}

                    </Section>


                </VerticalContainer >
            </Hidden>
            <VerticalContainer>

                <Hidden smUp>
                    <Section RECAP flex={1} height="100%">

                        < RecapSection toplabel="Se connecter" props={props} eventData={eventData} closeHandler={currentProcess?.closeHandler}/>

                    </Section>


                </Hidden>

                <Section flex={50}>
                    < LoginContainer props={props} />
                </Section>




            </VerticalContainer>

        </Box>
    );
}






const LoginContainer = ({ props }) => {




    const { currentProcess, localState, setLocalState } = props

    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

   
    const classes = useStyles()



    return (
        <Box className={classes.sectionContainer} >


            <Box width="100%" borderRadius="10px"  bgcolor="white"  >
                <form className={classes.root} noValidate autoComplete="on" onSubmit={(event) => {
                    event.preventDefault()
                    currentProcess.workflowLogic.onClickMainButton(props)
                }
                }>
                    <Box   display="flex" width="100%" height="100%" flexDirection="column" justifyContent="space-between" alignContent="center" alignItems='center' >
                        <Box className={classes.loginContainer2}>
                            <Typography variant="h4" style={{ fontWeight: 'bold' }}>
                            Connectez vous  votre compte
                        </Typography>
                        </Box>

                        <Box  padding="0 20px 0 20px" width="100%">

                            <NewValidationTextField
                                fullWidth
                                id="email"
                                label="e-mail"
                                name="E-mail"
                                autoComplete="email"
                                autoFocus
                                InputProps={{

                                    className: classes.input,
                                    autoFocus: localState[STAGE_SUBSCRIPTION] === 'STAGE_0A' ? true : false
                                }}
                                InputLabelProps={{
                                    shrink: true
                                }}
                                variant="outlined" value={localState[EMAIL]} onChange={onChange(EMAIL)} />



                        </Box>



                        <Box   marginTop="5px" width="100%" padding="0 20px 0 20px">


                            <NewValidationTextField

                            
                                margin="normal"
                                fullWidth
                                name="password"
                                label="Mot de passe"
                                type="password"
                                id="password"
                                InputProps={{
                                    className: classes.input,
                                    autoFocus: localState[STAGE_SUBSCRIPTION] === 'STAGE_SUBSCRIPTION' ? true : false
                                }}
                                InputLabelProps={{
                                    shrink: true
                                }}
                                variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />
                        </Box>


                        <Box  width="100%" display="flex" padding="0px 20px 0px 20px" flexDirection="column" justifyContent="center">
                            <StyledButtonFilled
                                type="submit">
                                <Typography className={classes.buttonTypography}>
                                    CONNEXION
                            </Typography>

                            </StyledButtonFilled>
                            <StyledUnderlinedButton onClick={() => {

                                currentProcess.workflowLogic.onClickThirdButton(props)
                            }}>

                                <Box textAlign="center" padding="20px" borderBottom = "1px solid black" width="80%">


                                    <Typography className={classes.buttonTypography2}>
                                        {"Mot de passe oubli ?".toLocaleUpperCase()}
                                    </Typography>
                                </Box>
                            </StyledUnderlinedButton>

                        </Box>




                    </Box>

                </form>
            </Box>

            <Box width="80%" textAlign="center"  >
                <DisplayError message={localState[ERROR_MESSAGE]} />

            </Box>


            <Box  borderRadius="10px" bgcolor="white"  padding="0 0 20px 0"  width="100%" display="flex" flexDirection="column" align="center" alignItems="stretch" justifyContent="start" >
                <Box padding='5px'>
                    <Typography variant="h4" style={{ fontWeight: 'bold' }}>
                        Nouveau sur Pekoia ?
                </Typography>

                </Box>

                <Box padding="10px 20px 10px 20px">
                <StyledButton
                    onClick={() => currentProcess.workflowLogic.onClickSecondButton(props)}>
                    <Typography className={classes.buttonTypography}>
                        {"S'inscrire".toUpperCase()}
                    </Typography>

                </StyledButton>
                </Box>


            </Box>
        </Box>
    );
}




