
import React from 'react'
import Box from '@mui/material/Box'
import {
    ThemeProvider,
    StyledEngineProvider,
    createTheme,
    responsiveFontSizes,
    adaptV4Theme,
} from '@mui/material/styles';
import { useState } from 'react'
import { getModalStyle, useStyles } from '../../style/modalsStyle'
import CloseIcon from '@mui/icons-material/Close'


const ModalContainer = (props) => {

    const { closeHandler, children } = props

        //theme adustment used for the input textField
        let theme = createTheme(adaptV4Theme({
            overrides:{
                MuiOutlinedInput: {
                    input:{
                        marginLeft: '30px',
                        paddingTop: '12px',
                        paddingBottom: '12px',
                        marginRight: '-1px',
                    }
                }            
            }
       
          }))

          theme = responsiveFontSizes(theme)

    
    const classes = useStyles()


    return (
        <StyledEngineProvider injectFirst>
            <ThemeProvider theme={theme}>  
            <div  className={classes.wrapper}>
                <Box  display="flex" flexDirection="column" className={classes.modalContainer}>
                    
                    <Box className={classes.menuBar} onClick={closeHandler} >
                        <CloseIcon className={classes.menuIcon}/>
                    </Box>
                    <Box  className={classes.contentContainer}>
                        
                        {children}
                    </Box>

                </Box>
            </div>
            </ThemeProvider>
        </StyledEngineProvider>
    );
}

export default ModalContainer