const startingWidth = 400
const endWidth = 700
const breakpoints = [600, 960, 1280]

export const var_a = (endWidth-startingWidth)/(breakpoints[1]-breakpoints[0])
export const var_b = startingWidth - var_a * breakpoints[0]

const widthFormula = (width) => {
    if (width < breakpoints[0])
        return ('100%')
    
    return Math.min(endWidth, Math.max(startingWidth, var_a * width+ var_b))
    }

export default widthFormula