import { useContext } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import Hidden from '@mui/material/Hidden'
import { grey } from '@mui/material/colors'

import { StyledButtonFilled } from '../../style/modalsStyle'
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import BannerLeft from './components/bannerLeft'
import Section from './components/section'


import React from 'react'
import ClipLoader from "react-spinners/PropagateLoader"
import {  css } from '@emotion/react'
import {SPINNER, EMAIL} from '../../subscriptionModal/general'
import {
    useWindowSize,
   
} from '@react-hook/window-size'
import widthFormula from '../helpers/computing'
import RecapSection from './components/recapSection'

import { initialState } from '../../subscriptionModal/general'
import { AuthContext } from '../../../context/general'
import VerticalContainer from './components/verticalContainer'




const WithAccount = (props) => {





    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <MainContainer props={props} />
        </ModalContainer>)

}

export default WithAccount

const MainContainer = ({ props }) => {

    const { authState } = useContext(AuthContext)

    const eventData = authState.showSubscriptionModal

    const [width, height] = useWindowSize()
    void height



    return (
        <Box display="flex"  flexDirection="row" justifyItems="stretch" width={widthFormula(width)}>


            <Hidden mdDown>
            <VerticalContainer  width="200px" margin="0 20px 0 0px">
    
                <Section RECAP_LARGE >
                        <BannerLeft props={props} eventData={eventData}/>
                    </Section>
                    <Section PRICE_LARGE>

                    </Section>

                </VerticalContainer >
            </Hidden>
            <VerticalContainer >
            <Hidden mdUp>
                    <Section RECAP>
                        < RecapSection />
                    </Section>

                </Hidden>

              
                <Section CONNECTION >

                    <ConnexionSection CONNECTION props={props} />

                </Section>
                

                <Section PRICE_SMALL>

                    <NoConnexionSection props={props}/>

                </Section>


            </VerticalContainer>

        </Box>
    );
}













const ConnexionSection = ({ props }) => {

    const { authState } = useContext(AuthContext)
   

    const override = css`
        position: relative;
        right: 0px;
        bottom: 0px;
        z-index: 1;
        `;



    const { currentProcess, localState } = props

    return (<Box  width="95%" bgcolor="white" padding="15px" display="flex" flexDirection="row" alignItems="center" justifyContent="space-between">
      <Box  width="100%" display="flex" flexDirection="column" alignContent="center">
          <Box marginTop="30px">
          <Typography style={{ color: "black", fontWeight: "bold", fontSize: "1rem", lineHeight: "1rem" }}>
            Paiement scuris
        </Typography>

          </Box>
        
       
        <Box display="flex" flexDirection="row" justifyContent="center" alignContent="center">
            <Typography style={{ color: "black", fontSize: "0.8rem", lineHeight: "1rem", padding: "10px" }}>
                Vous allez tre redirigs vers notre page de paiement scuris
        </Typography>
   

        </Box>
        <Box marginTop="50px">
        <StyledButtonFilled onClick={() => {
            currentProcess.workflowLogic.onClickMainButton({ ...props, authState: authState })}}>
            Effectuer le paiement
          
        </StyledButtonFilled>

        </Box>
        

        <Box padding="50px" textAlign="center">
        {(localState[SPINNER] || true) && <ClipLoader css={override} />}
        </Box>    
            
       

    </Box>
    </Box>)
}

const NoConnexionSection = ({props}) => {

    const {localState} = props
    const theme = useTheme()
    const matches = useMediaQuery(theme.breakpoints.up('sm'))
    void matches

   
 
    return (<Box  height="200px" padding="0px 0 20px 0" width="100%" display="flex" flexDirection="row" >
        <Box padding="20px">
            
            <Typography style={{ color: "black", fontSize: "0.8rem", lineHeight: "1.3rem" }}>
                Vous recevrez un email de confirmation  l'adresse email suivante:
            </Typography>
            <Box marginTop="10px" textAlign="center">
            <Typography style={{ fontWeight: 'bold', fontSize: '1.1rem', color: grey[600] }}>
            {localState[EMAIL]}
            </Typography>

            </Box>
           

        </Box>



    </Box>)
}
