

import React, { useContext } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import { DisplayError } from '../../helpers/general'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import FrontOfficeTheme from '../../../style/globalTheme'

import BannerLeftLogin from './components/bannerLeftLogin'



import Hidden from '@mui/material/Hidden'
import { grey } from '@mui/material/colors'

import RecapSection from './components/recapSection'
import { useStyles, StyledButtonFilled, NewValidationTextField } from '../../style/modalsStyle'

import { EMAIL, ERROR_MESSAGE, STAGE_SUBSCRIPTION, NAME } from '../../subscriptionModal/general'


import BannerLeft from './components/bannerLeft'
import VerticalContainer from './components/verticalContainer'
import { AuthContext } from '../../../context/general'

import Section from './components/section'



import {
    useWindowSize,

} from '@react-hook/window-size'

import { initialState } from '../../subscriptionModal/general'






const SubscriptionHomePage = (props) => {




    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <StyledEngineProvider injectFirst>
                <ThemeProvider theme={FrontOfficeTheme}>
                    <MainContainer props={props} />
                </ThemeProvider>
            </StyledEngineProvider>
        </ModalContainer>
    );

}

export default SubscriptionHomePage

const MainContainer = ({ props }) => {
    const { currentProcess, localState } = props
   
    const [width, height] = useWindowSize()
    const { authState } = useContext(AuthContext)



    const eventData = authState.showSubscriptionModal
    void height
    void width


    return (
        <Box display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="93%">
            <Hidden mdDown>
                <VerticalContainer width="200px" margin="0 20px 0 0px">

                <Section RECAP_LARGE  bgcolor="black" height="100%">
                        {localState[NAME] === 'LOGIN' && <BannerLeftLogin props={props} />}
                        {localState[NAME] === 'SUBSCRIPTION' && <BannerLeft props={props} eventData={eventData} />}
                    </Section>


                </VerticalContainer >
            </Hidden>
            <VerticalContainer >

                <Hidden mdUp>
                    <Section RECAP flex={1} height="100%">

                        < RecapSection toplabel="Rinitialiser" props={props} eventData={eventData} closeHandler={currentProcess?.closeHandler} />

                    </Section>


                </Hidden>

                <Section bgcolor="white" flex={50}>
                    < LoginContainer props={props} />
                </Section>




            </VerticalContainer>

        </Box>
    );
}






const LoginContainer = ({ props }) => {




    const { currentProcess, localState, setLocalState } = props

    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

    const showSecondTextAread = (localState[STAGE_SUBSCRIPTION] === STAGE_SUBSCRIPTION) ? true : false

    const classes = useStyles()
    return (
        <Box className={classes.sectionContainer}  >
            <Box borderRadius="10px" bgcolor="white" padding="20px">
                <Typography variant="h4" style={{ fontWeight: "bold" }}>
                    Mot de passe oubli ?


            </Typography>

                <Box marginTop="10px" >
                    <Box width="100%" wrap="wrap">
                        <Typography style={{ color: grey[500] }}>
                            Renseignez ladresse e-mail de votre compte Pekoia ci-dessous :
            </Typography>

                    </Box>


                </Box>


                <Box marginTop="5%" >


                    <form className={classes.root} noValidate autoComplete="on" onSubmit={(event) => {
                        event.preventDefault()
               
                        currentProcess.workflowLogic.onClickMainButton(props)
                    }
                    }>
                        <Box margin="auto" display="flex" height="100%" flexDirection="column" alignItems='start'>





                            <Box marginBottom="15px" marginTop="30px" width="100%">

                                <NewValidationTextField
                                    name="email"
                                    id="emailfield"
                                    fullWidth

                                    type="text"
                                    label="e-mail"
                                    autoComplete="section-blue email"
                                    InputProps={{

                                        className: classes.input,
                                        autoFocus: true
                                    }}

                                    InputLabelProps={{
                                        shrink: true
                                    }}
                                    variant="outlined" value={localState[EMAIL]} onChange={onChange(EMAIL)} />



                            </Box>



                            <Box   padding="30px 0px 10px 0px" width="calc(100% )" display="flex" flexDirection="column" justifyContent="center">

                                <StyledButtonFilled
                                    type="submit">
                                    <Typography className={classes.buttonTypography}>
                                        {"rinitialiser mot de passe".toUpperCase()}
                                    </Typography>

                                </StyledButtonFilled>
                            </Box>
                            <Box width="80%" textAlign="center">
                                <DisplayError message={localState[ERROR_MESSAGE]} />

                            </Box>
                        </Box>
                    </form>
                </Box>

                {showSecondTextAread && <Box textAlign="right">

                </Box>}
            </Box>

        </Box>
    );
}





