export const COUNT = 'COUNT'
export const SPINNER = 'SPINNER'


// DEFINING PROCESSES LOGIC
// availables prosp for functions are:
// localState: availables "internal states of the navigation handler": EMAIL, PASSWORD, DIGITS, ERROR_MESSAGE, STAGE
// setLocalState: setter for internal states
// currentProcess: give access to routines that will allow to update the global state: setAuthData

// helper for improved clarity of code
const localStateAPI = (props) => ({
     moveTo : ( newValue) => props.setLocalState({...props.localState, [ERROR_MESSAGE]: '', [STAGE] : newValue}),
     increaseCount : ( ) => props.setLocalState({...props.localState, [COUNT] : props.localState+1}),
     displayErrorMessage : ( message) => props.setLocalState({...props.localState, [ERROR_MESSAGE] : message})
})

const workflowLogic0A = {
    onClickMainButton: (props) => {

      
        
        const {localState} = props
        if (testEmail(localState[EMAIL])){
            const callback = {
                available : ()=>localStateAPI(props).displayErrorMessage("Il n'y aucun compte Pekoia associ  cet email"),
                notConfirmed: ()=>localStateAPI(props).moveTo(STAGE_3A),
                notAvailable: ()=>localStateAPI(props).moveTo(STAGE_0B)
               
            }
            testIfEmailExists(localState[EMAIL], callback)
        }
        else  localStateAPI(props).displayErrorMessage('format email incorrect')

      
        //localStateAPI(props).moveTo(STAGE_0B)
    },
    onClickSecondButton : (props) => {
        const {localState} = props
          
        if (testEmail(localState[EMAIL])){
            const callback = {
                available : ()=>localStateAPI(props).moveTo(STAGE_2A),
                notConfirmed: ()=>console.log('B'),
                notAvailable: ()=> localStateAPI(props).displayErrorMessage('Cet email a dja t utilis.')
               
            }
            testIfEmailExists(localState[EMAIL], callback)
           
        }
        //'format email incorrect'
        else localStateAPI(props).displayErrorMessage('format email incorrect')
    }
}

void workflowLogic0A



const workflowLogic0B = {
    onClickMainButton: (props) => {


      
      
        const {localState, setLocalState, currentProcess} = props
        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            password: localState.PASSWORD,
            callback: (data)=>{
                currentProcess.setAuthData(data)
                currentProcess.closeHandler()
                fetchUserData(data, currentProcess.setAuthData)
            },
            onError: () => setLocalState({...localState, [ERROR_MESSAGE]: 'problme de connexion-A'})
        }).signIn()

    },
    onClickSecondButton : ({localState, setLocalState}) => setLocalState({...localState, [STAGE]: STAGE_0A}),
    onClickThirdButton : (props) => {
        const {localState, setLocalState} = props
        AuthAPI({
                email: localState.EMAIL,
                callback: ()=>localStateAPI(props).moveTo(STAGE_3C),
                onError: (err) => {
                    
                    console.log('@ERROR')
                    console.log(err)
                    
                    setLocalState({...localState, [ERROR_MESSAGE]: 'problme de connexion-B'})}
            }).forgot()
        
    }
}
      
