const workflowLogic3 = {
    onClickMainButton: (props) => {
        localStateAPI(props).displayErrorMessage('')
        const {localState, setLocalState, currentProcess} = props

    

        localStateAPI(props).displayErrorMessage('')

        AuthAPI({
            email: localState.EMAIL,
            password: localState.PASSWORD,
            digits: localState.DIGITS.join(''),
            callback: ()=> {
                
                AuthAPI({
                email: localState.EMAIL,
                password: localState.PASSWORD,
                callback: (data)=>{
                    currentProcess.setAuthData(data)
                    currentProcess.closeHandler()
                    fetchUserData(data, currentProcess.setAuthData)
                },
                onError: () => setLocalState({...localState, [ERROR_MESSAGE]: 'problme de connexion-D'})
                 }).signIn()
              
        
            },
            onError: (err) => {
                setLocalState({...localState, [ERROR_MESSAGE]: 'problme de connexion-E'})
            }
        }).confirmSignUp()

    },
    onClickButton: (props) => {
        const {localState} = props 
        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                localStateAPI(props).displayErrorMessage('un nouveau code vous a t envoy')
            },
            onError: (err) => {
                console.log('@ERROR-F')
                console.log(err)
                localStateAPI(props).displayErrorMessage('problme de connexion')}
        }).resendSignUp()
    }
}



