const worflowScope = {
    pages : [homePageLogic, confirmationPageLogic, saisieMotDePassePageLogic, digitInputPageLogic]
}

const workflow = []

worflowScope.pages.forEach(pageItem=>{
    pageItem.stageLogic.forEach(stage=>workflow.push({
        page: pageItem.page,
        name: stage.name,
        workflowLogic: stage.workflowLogic,
        component: pageItem.component
    }))
})

export default workflow

