import { Auth } from "aws-amplify"





export const testIfEmailExists = (email, callBack) => {

    //  email : string
    //  callBack : {
    //     available: handler0,         // available email
    //     notConfirmed: handler1,     // email has not been confirmed 
    //     notAvailable: handler2      // already used
    // }


    Auth.signIn(email, '123')
        .then(data => { })          // cannot happen
        .catch(err => {
            if (err.message.includes('exist')) { callBack.available(err) }
            else if (err.message.includes('not confirmed')) { callBack.notConfirmed(err) }
            else { callBack.notAvailable(err) }
        })
}



const signUp = ({email, password, callback, onError}) => {
    Auth.signUp({
        username: email,
        password: password,
        attributes: {
            email: email          
        }
    })
        .then(data => callback(data))
        .catch(
            (err) => {
                
                onError(err)
            
            }
        )
}

const resendSignUp = ({ email, callback, onError }) => {



    Auth.resendSignUp(email)
        .then((data) => {
            callback(data)
        })
        .catch(
            (err) => onError(err)
        )
}

const forgot = (props) => {

    const { email, callback, onError } = props

  
    




    Auth.forgotPassword(email)
        .then((data) => {
            callback(data)
        })
        .catch(
            (err) => onError(err)
        )
}


const confirmSignUp = ({email, digits, callback, onError}) => {

    Auth.confirmSignUp(email, digits)
        .then((data) => {
            callback(data)
        })
        .catch(
            (err) => onError(err)
        )
}


