const signIn = ({ email, password, callback, onError }) => {

    Auth.signIn(email, password)
        .then(
            data => callback(data)
        )
        .catch((err) => onError(err))
}

const forgotPasswordSubmit = ({ email, digits, password, callback, onError }) => {

    Auth.forgotPasswordSubmit(email, digits, password)
        .then(
            data => callback(data)
        )
        .catch((err) => onError(err))
}




// wrapper
export const AuthAPI = (props) => {

    return (
        {
            resendSignUp: () => resendSignUp(props),
            forgot: () => forgot(props),
            confirmSignUp: () => confirmSignUp(props),
            signIn: () => signIn(props),
            signUp: () => signUp(props),
            forgotPasswordSubmit: () => forgotPasswordSubmit(props)
        }
    )




}