import React from "react"
import md5 from 'md5'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import FacebookIcon from '@mui/icons-material/Facebook'
import EmailIcon from '@mui/icons-material/Email'
import WhatsAppIcon from '@mui/icons-material/WhatsApp'
import TwitterIcon from '@mui/icons-material/Twitter'
import makeStyles from '@mui/styles/makeStyles';
import { isMobile } from 'react-device-detect'


import {
    FacebookShareButton,
    TwitterShareButton,
    WhatsappShareButton,
    EmailShareButton

} from 'react-share'




import { grey, green } from '@mui/material/colors';




// BEGIN: CUSTOMIZE-IT
const innerWidth = 5
const iconSize = 30
const twitterHandle = "Pekoia"
// END: CUSTOMIZE-IT

const getFormatted = (duree) => {
    if (duree === 60)
        return '1h'
    if (duree === 120)
        return '2h'
    if (duree === 90)
        return '1h30'
    else
        return duree + 'mn'
}





const ShareIt = ({ url, title, tags, keyPromo, authState, pastEvent, data }) => {


    const dureeString = data?.pageEvent?.duree ? getFormatted(data.pageEvent.duree) : getFormatted(60)
    const isReplayAvailable = data?.pageEvent?.replayStatus === 'AVAILABLE' ? true : false
    const buttonMessage = data?.pageEvent?.replayStatus === 'AVAILABLE' ? 'Acheter le replay' :"S'inscrire"

   
    

    const shareUrl = `Je me suis inscrit  une confrence sur Pekoia. Si tu veux te joindre  moi ${authState?.user?.id  ? "et bnficier d'une remise  ta premire inscription":""}, inscris-toi via le lien suivant:\n\n
          ${url}${authState?.user?.id ? `?shareKey=${authState.user.id}&cat=INV&signature=${md5('Signature!' + authState.user.id)}` : ''}`
    const classes = useStyles()

 



    return (

        <Box marginBottom={isMobile ? '30px' : 0}>



            <Box display="flex" flexDirection="row" justifyContent="center" marginTop="30px" alignContent="center" alignItems="center">
                <Box marginRight="20px">
                    <Typography style={{ color: grey[500], fontSize: '0.8rem' }}>
                        partagez  {!isMobile && " sur les rseaux sociaux"}
                    </Typography>
                </Box>
                <Box display="flex" flexDirection="row" width="auto" alignContent="center" justifyContent="center" padding="0px 5px 0 5px">
                    <Box width={iconSize + innerWidth / 2} >
                        <FacebookIcon style={{ fontSize: iconSize }} />
                    </Box>
                    <FacebookShareButton url={url} >
                        <Typography className={classes.iconText}>
                            facebook
                        </Typography>

                    </FacebookShareButton>


                </Box>

                <Box display="flex" flexDirection="row" width="auto" alignContent="center" justifyContent="center" padding="0px 5px 0 5px">
                    <Box width={iconSize + innerWidth / 2} >
                        <TwitterIcon style={{ fontSize: iconSize }} />
                    </Box>
                    <TwitterShareButton url={url} title={title} via={twitterHandle}>
                        <Typography className={classes.iconText}>
                            twitter
                        </Typography>

                    </TwitterShareButton>


                </Box>

            </Box>
            {(!pastEvent || isReplayAvailable) && <Box display="flex" flexDirection="row" justifyContent="center" marginTop="5px" alignContent="center" alignItems="center">
                <Box marginRight="20px">
                    <Typography style={{ color: grey[500], fontSize: '0.8rem' }}>
                        suggrez {!isMobile && " la confrence  un ami"}
                    </Typography>
                </Box>
                <Box display="flex" flexDirection="row" width="auto" alignContent="center" justifyContent="center" padding="0px 5px 0 5px">
                    <Box width={iconSize + innerWidth / 2} >
                        <WhatsAppIcon style={{ fontSize: iconSize }} />
                    </Box>
                    <WhatsappShareButton url={shareUrl} >
                        <Typography className={classes.iconText}>
                            whatsapp
                        </Typography>

                    </WhatsappShareButton>


                </Box>
                <Box display="flex" flexDirection="row" width="auto" alignContent="center" justifyContent="center" padding="0px 5px 0 5px">
                    <Box width={iconSize + innerWidth / 2} >
                        <EmailIcon style={{ fontSize: iconSize }} />
                    </Box>
                    <EmailShareButton url={shareUrl} >
                        <Typography className={classes.iconText}>
                            email
                        </Typography>

                    </EmailShareButton>


                </Box>

            </Box>}
        </Box>)
}

export default ShareIt

