import { osName } from "react-device-detect"

export const EMAIL = 'EMAIL'
export const PASSWORD = 'PASSWORD'
export const NAME = 'NAME'
export const DIGITS = 'DIGITS'
export const SPINNER = 'SPINNER'
export const STAGE_LOGIN = 'STAGE_LOGIN'
export const ERROR_MESSAGE = 'ERROR_MESSAGE'
export const CONSENT = "CONSENT"
export const COUNT = 'COUNT'
export const PAGE_LOGIN = 'PAGE_LOGIN'
export const MODALNAME = 'LOGIN'
export const PAGE_CONFIRMATION = "PAGE_CONFIRMATION"

const PAGE_NEWACCOUNT = "PAGE_NEWACCOUNT"


const nullValuesMap = [{
    name: NAME,
    value: MODALNAME,
    

},

{
    name: STAGE_LOGIN,
    value: PAGE_LOGIN,
  
},
{
    name: EMAIL,
    value: ''
},
{
    name: PASSWORD,
    value: ''
},
{
    name: COUNT,
    value: 0
},
{
    name: SPINNER,
    value: false
},
{
    name: DIGITS,
    value: ['', '', '', '', '', '']
},
{
    name: CONSENT,
    value: true
}]

const nullValue = (name, variant) => {

   

    if ((variant === 2)&& name === STAGE_LOGIN)
    {

        return PAGE_NEWACCOUNT
    }
        


    const tempResult = nullValuesMap.find(variable => variable.name === name)
   
    return tempResult.value
}




const localStateVariables = [SPINNER, NAME, EMAIL, PASSWORD, DIGITS, STAGE_LOGIN, CONSENT]


const tempState = (authState) => {
    const state = {}
    const variant = authState.showLoginModal


   

    localStateVariables.forEach(name => { state[name] = nullValue(name, variant) })


   


    if (authState.user?.id)
        state[STAGE_LOGIN] = state[STAGE_LOGIN] + 'B'
    else {
       
        state[STAGE_LOGIN] = state[STAGE_LOGIN] + 'A'
   
    }

        


    return (state)
}


export const initialState = (authState) => tempState(authState)

