
import React, { useEffect } from 'react'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import { grey, green, yellow, red } from '@mui/material/colors'

import { useContext } from 'react'
import { AuthContext } from '../../../../context/general'
import { GatsbyImage } from "gatsby-plugin-image"
import { formatMapping } from '../../../../templates/components/events/sectionEvent'
import { getReduction } from '../../../../helpers/generalData'



const BannerLeft = ({ eventData, props, promoMainData }) => {

    // useEffect(()=>{

    //     console.log('@PROS')
    //     console.log(props)

    // },[props])







    const isValid = (props.currentProcess?.authState.parsed?.valid && eventData.prix >= 5) || (promoMainData?.promoCode == 4)
    const hideInitialPrice = (promoMainData?.promoCode == 4) ? true : false
    const remiseConferencier = (promoMainData?.promoCode == 4) ? Math.round(eventData?.prix * 0.15 * 100) / 100 : 0

    const quickTruncate = (myText, NbChar) => {


        if (myText == null)
            return ""
        if (myText.length > NbChar)
            return myText.slice(0, NbChar) + ' ...'
        return myText

    }




    return (<Box width="100%" height="100%" color="white" bgcolor="#191919" justifyItems="stretch" display="flex" flexDirection="column" justifyContent="space-between">

        {eventData?.eventId && <Box margin="15px">
            <GatsbyImage
                image={eventData.image}
                alt={eventData.title1 ? eventData.title1 : ''}
                loading="lazy"
                style={{ zIndex: 2, objectFit: 'cover', width: "9vw", maxWidth: "100%", height: "9vw" }}
            />

        </Box>}

        {eventData?.cycleId && <Box margin="15px" >
            <img
                src={eventData.cycleImageUrl}
                alt={eventData.title1}
                style={{ zIndex: 2, objectFit: 'cover', width: "13vw", maxWidth: "100%", height: "9vw" }}
            />

        </Box>}
        <Box margin="15px" marginTop="20px" >
            <Box display="flex" flexDirection="row" flexWrap="wrap">
                <Box>


                    <Typography style={{ fontSize: "0.9rem", fontWeight: "bold", lineHeight: "1.0rem" }} >
                        {quickTruncate(eventData?.title1, 60)}
                    </Typography>

                </Box>
                <Box marginTop="5px">



                    <Typography style={{ fontSize: "0.8rem", color: grey[300], lineHeight: '0.9rem' }} >
                        {eventData?.title2}
                    </Typography>
                </Box>


            </Box>

            {eventData.isReplayAvailable && <Box marginTop="5px" width='150px' borderRadius='5px' display="flex" flexDirection="row" justifyItems="center" justifyContent="center">
                <Typography style={{ fontSize: "0.7rem", color: 'black', backgroundColor: grey[400], padding: '2px 5px 2px 5px', borderRadius: '5px' }}>
                    confrence en replay
                </Typography>
            </Box>}

            {eventData?.outil && !eventData.isReplayAvailable && <Box marginTop="5px">
                <Typography style={{ fontSize: "0.7rem", color: grey[400] }}>
                    {formatMapping.find(elem => elem.format === eventData?.outil).label}
                </Typography>
            </Box>}
            <Box marginTop="20px">
                <Typography style={{ color: grey[200], fontWeight: 700, fontSize: "0.7rem", lineHeight: "0.7rem" }}>
                    {eventData?.formateur?.prenom}{" "} {eventData?.formateur?.nom}

                </Typography>

            </Box>

        </Box>
        <Box display="flex" flexDirection="row" justifyContent="space-around" marginTop="10px" >
            <Box display="flex" flexDirection="column" >
                <Typography>
                    {eventData.date && eventData.date.dateShort.split('/').slice(0, 2).join('/')}
                </Typography>
                <Typography style={{ fontSize: '0.6rem' }}>
                    {eventData.date && eventData.date.weekDay.split(' ')[0].toUpperCase()}
                </Typography>

            </Box>
            <Box>
                <Typography>
                    {eventData.date && eventData.date.time}
                </Typography>
            </Box>

        </Box>
        {eventData.eventId && <Box margin="10px 15px 15px 15px">
            <Typography style={{ fontSize: '0.7rem', color: grey[400] }}>
                dure: {eventData.dureeString}
            </Typography>
        </Box>}
        {!eventData.isReplayAvailable && <Box display="flex" flexDirection="row" justifyContent="space-around" padding="10px" marginTop="10px" >
            <Box display="flex" flexDirection="column" justifyContent="center">
                {eventData.prix > 0 && <Typography style={{ fontSize: '0.8rem' }}>

                    {(eventData.date || eventData.cycleId) && "Total"}
                </Typography>}
                {eventData.prix === 0 && "GRATUIT"}



            </Box>

            <Box padding="10px" flexDirection="row" display="flex">
                {eventData.prix > 0 && <Typography style={{ textDecorationStyle: hideInitialPrice ? 'double' : 'none', fontWeight: hideInitialPrice ? "normal" : "bold", textDecoration: hideInitialPrice ? 'line-through' : 'none' }}>

                    {(eventData.date || eventData.cycleId) && eventData.prix + ""}
                </Typography>}
                {isValid && eventData.prix > 0 && <Typography style={{ fontWeight: "bold", color: red[600] }}>
                    {" "}{(eventData.date || eventData.cycleId) && (eventData.prix - (promoMainData.promoCode ? remiseConferencier : getReduction(props.parsed, props.currentProcess?.authState))) + ""}
                </Typography>}

            </Box>


        </Box>}
        {eventData.isReplayAvailable && <Box display="flex" flexDirection="row" justifyContent="space-around" padding="10px" marginTop="10px" >
            <Box display="flex" flexDirection="column" justifyContent="center">
                {eventData.prix > 0 && <Typography style={{ fontSize: '0.8rem' }}>

                    {(eventData.date || eventData.cycleId) && "Total"}
                </Typography>}
                {eventData.prix === 0 && "GRATUIT"}



            </Box>

            <Box padding="10px" flexDirection="row" display="flex">
                {eventData.prix > 0 && <Typography style={{ textDecorationStyle: hideInitialPrice ? 'double' : 'none', fontWeight: hideInitialPrice ? "normal" : "bold", textDecoration: hideInitialPrice ? 'line-through' : 'none' }}>

                    {(eventData.date || eventData.cycleId) && eventData.prix + ""}
                </Typography>}
                {isValid && eventData.prix > 0 && <Typography style={{ fontWeight: "bold", color: red[600] }}>
                    {" "}{(eventData.date || eventData.cycleId) && (eventData.prix - (promoMainData.promoCode ? remiseConferencier : getReduction(props.parsed, props.currentProcess?.authState))) + ""}
                </Typography>}

            </Box>


        </Box>}


    </Box>)
}

export default BannerLeft