
import { sanboxPush } from '../../helpers/main'
import { randomId } from '../../helpers/codeGen'
import API_DynamoDB from '../../../helpers/DBAPI'
import { handleFreeSubscription, fetchCreateClientAttribute } from '../../../helpers/fetchers'
import cookies from 'js-cookie'
import { DateTime } from 'luxon'
// import { Analytics } from 'aws-amplify'
//import { lemonwayId } from '../../helpers/migration'
import { successSuffix, errorSuffix, cancelSuffix, pekoiaUrl } from '../../helpers/migration'
import { getReduction } from '../../../helpers/generalData'
import { navigate } from 'gatsby'








export const initiatePaiement = (props) => {

  

  const postProcess = (props) => {

 

    let cardId = null
    if (props.cardId)
      cardId = props.cardId.lemonwayCardId




    const clientId = props.authState.user.id
    const transactionId = randomId(16)
    const referenceId = randomId(12)

    const commentSuffix = props.guest ? ' /guest' : ''

    const computeFees = (price) => {

      const TVA = 0.2           // 20% TVA 
      const baseFee = 0.3       // 0.3 EUR frais de tx minimum 
      let cutEvent = 0.12     // 12% pour les vnements solo 
      let cutCycle = 0.10     // 10% pour les cycles


      if (props.eventData.isReplayAvailable){

        // HANDLE REPLAY WITH DIFFERENT COMMISSIONS

        cutEvent = 0.3
        cutCycle = 0.3


      }

      if (props.eventData.eventId)
        return Math.ceil((price * 100 * cutEvent + baseFee * 100) * (1 + TVA))

      if (props.eventData.cycleId)
        return Math.ceil((price * 100 * cutCycle + baseFee * 100) * (1 + TVA))

    }

    const cycleId = props.eventData.cycleId
    let eventTime = null
    if (cycleId) {

      // console.log('@DEBUG CYCLE')
      // console.log(props)

    }

    else {

      eventTime = props.eventData.date.luxonDate.ts / 1000


    }

    const now = DateTime.now().ts / 1000

    let scheduledTask = 'SENDMAIL'
    let scheduledDate = 0


    if (props.eventData.isReplayAvailable){
      // HANDLE REPLAY - DO NOT SEND ANY CONFIRMATION MAIL
      scheduledTask = 'NONE'
    }


   

    const accountId = props.eventData.lemonwayId ? props.eventData.lemonwayId : 'undetermined'
    const refererAccountId = props?.authState?.parsed?.shareKey

    const remiseConferencier = props.promoCode == 4 ? Math.round(props.eventData?.prix * 0.15*100) / 100  : 0
    const totalComputedFees = computeFees(props.eventData.prix)
    const commissionAmount = props.remise > 0 ? 0 : totalComputedFees
    const totalAmount = props.eventData.prix ? (props.eventData.prix - props.remise) * 100 - remiseConferencier * 100 : 0
    const potentialIcentive = props.eventData.prix * 100 - totalComputedFees - totalAmount
    const pekoiaIncentive = props.eventData.prix > 0 && potentialIcentive > 0 ? potentialIcentive : 'NONE'
    const pekoiaPromotioncode = props.parsed.valid ? props.parsed.cat : 'NONE'
    const remiseComment = props.remise > 0 ? `// TFC: ${totalComputedFees} // cat: ${pekoiaPromotioncode} // PR: ${props.remise * 100} //  CI: ${refererAccountId}` : ""
    const remiseConferencierComment = remiseConferencier > 0 ? `// RC: ${totalComputedFees} // promo type 4: ${props.promoId}` : ''
    const orderComment = `Order number ${transactionId}${remiseComment}${remiseConferencierComment}`

    

    const lemonwayDataRaw = {
      "returnUrl": pekoiaUrl + "/" + successSuffix,
      "errorUrl": pekoiaUrl + "/" + errorSuffix,
      "cancelUrl": pekoiaUrl + "/" + cancelSuffix,
      "captureDelayedDays": 0,
      "moneyInNature": 0,
      "reference": referenceId,                                         // cette cle est modifie lors de la cration de la transaction dans dynamoDB
      "accountId": accountId,
      "totalAmount": totalAmount,
      "commissionAmount": props.eventData.prix ? commissionAmount : 0,
      "comment": orderComment,
      "autoCommission": false
    }

       // RECORD REFERER IN DDB
       if (props.isValidReferer){

       console.log(props.authState.user.id)

        const refererId = props.parsed.shareKey
      
        fetchCreateClientAttribute({
          clientId: refererId,
          key: 'INV',
          value: props.authState.user.id,
          state: 'PROCESSING',
          additionalValue: orderComment
        
        })

      


      }




    let lemonwayData = null

    if (cardId)
      lemonwayData = { ...lemonwayDataRaw, 'cardId': cardId.toString() } //.
    else
      lemonwayData = { ...lemonwayDataRaw, "registerCard": true }






    const pekoiaData = {
      cycleId: props.eventData.cycleId ? props.eventData.cycleId : 'undetermined',
      eventId: props.eventData.eventId ? props.eventData.eventId : 'undetermined',
      clientId: props.guest ? 'guest' : clientId,
      lemonwayId: props.eventData.prix === 0 ? 'FREE' : referenceId,
      status: props.eventData.prix === 0 ? 'SETTLED' : 'INITIATED'

    }

    // if (!cycleId){

    pekoiaData.scheduledTask = scheduledTask
    pekoiaData.scheduledDate = scheduledDate

    // }







    const sandBoxData = {
      payload: lemonwayData,
      serviceUrl: '/moneyins/card/webinit/',
      method: 'POST',
      claim: 'NULL',
      id: props.guest ? 'guest' : clientId
    }










    const onError = null
    void onError

    const callback = (transactionDetails) => {





      if (props.eventData.prix === 0) {

        const eventId = transactionDetails.id

        


        if (props.eventData.eventId !== 'undetermined') {

          handleFreeSubscription(props.eventData.eventId)
          //window.location.assign('https://www.pekoia.com/sandboxSuccess?response_wkToken='+eventId)

        

        navigate('/sandboxSuccess?response_wkToken='+eventId)


        }

        // setTimeout(() => {
        //   props.currentProcess.closeHandler()
        // }, 1500);



      }
      else {



        const callBackLemonway = (data) => {



          const { webKitToken, id } = data

          if (webKitToken) {

            void id
            const urlSandbox = `https://webkit.lemonway.fr/mb/pekoia/prod/?moneyintoken=${webKitToken}`

            window.location.href = urlSandbox
          }

        }

        const transactionId = transactionDetails.id

        const newlemonwayData = { ...lemonwayData, "reference": transactionId }
        const newSandBoxData = { ...sandBoxData, payload: newlemonwayData }



        
        //@ TOTO GET BACK
        sanboxPush(newSandBoxData, callBackLemonway)
      }
    }



    // handle real transactions: price > 0





    API_DynamoDB({
      data: pekoiaData,
      callback: callback,
      onError: console.log
    }).createTransaction()








  }




  if (props.parsed?.fetchResult) {
    props.parsed.fetchResult
      .then(data => {

       

      
        if (data){
          postProcess({...props, isValidReferer: true})
        }
        else {
          postProcess({...props, isValidReferer: false})
        }
        
       
      })
      .catch(err => {
        postProcess({...props, isValidReferer: false})
      })

  }
  else{
    postProcess({...props, isValidReferer: false})
  }












}
