import React from 'react'
import { useContext, useEffect, useState } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'

import Typography from '@mui/material/Typography'
import EditIcon from '@mui/icons-material/Edit';
import CloseIcon from '@mui/icons-material/Close';
import Button from '@mui/material/Button';




import Hidden from '@mui/material/Hidden'
import { grey, yellow, green, purple, blue } from '@mui/material/colors'
import CircularProgress from '@mui/material/CircularProgress';

import { StyledButtonFilled, useStyles, MiniValidationTextField, StyledMiniButton } from '../../style/modalsStyle'
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import Checkbox from '@mui/material/Checkbox'
import ClipLoader from "react-spinners/PropagateLoader"
import { css } from '@emotion/react'
import { SPINNER, EMAIL, NAME } from '../../subscriptionModal/general'
import BannerLeftLogin from './components/bannerLeftLogin'



import {
    useWindowSize,

} from '@react-hook/window-size'


import { initialState } from '../../subscriptionModal/general'
import { AuthContext } from '../../../context/general'
import Section from './components/section'
import BannerLeft from './components/bannerLeft'
import VerticalContainer from './components/verticalContainer'
import RecapSection from './components/recapSection'

const WithAccount = (props) => {



    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        console.log('@CLOSE HANDLER')
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <MainContainer props={props} closeHandler={closeHandler} />
        </ModalContainer>)

}

export default WithAccount

const MainContainer = ({ props, closeHandler }) => {



    const { isLogged, localState } = props

    const { authState } = useContext(AuthContext)



    const eventData = authState.showSubscriptionModal

    const [promoMainData, setPromoMainData] = useState({})



    const [width, height] = useWindowSize()

    if (isLogged === 'true')
        return null
    void height
    void width


    return (
        <Box display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="100%">


            <Hidden mdDown>
                <VerticalContainer width="200px" margin="0 20px 0 0px">

                    <Section RECAP_LARGE bgcolor="black" height="100%" >
                        {localState[NAME] === 'LOGIN' && <BannerLeftLogin props={props} />}
                        {localState[NAME] === 'SUBSCRIPTION' && <BannerLeft props={props} eventData={eventData} promoMainData={promoMainData} />}

                    </Section>
                </VerticalContainer >
            </Hidden>
            <VerticalContainer>

                <Hidden mdUp>
                    <Section RECAP flex={1} >

                        < RecapSection props={props} eventData={eventData} closeHandler={closeHandler} promoMainData={promoMainData}/>
                    </Section>

                </Hidden>
            
                <Section CONNECTION horizontal>


                    <ConnexionSection CONNECTION props={props} eventData={eventData} promoMainData={promoMainData} setPromoMainData={setPromoMainData} />



                </Section>

                <Section PRICE_SMALL flex={5} >

                    <NoConnexionSection props={props} eventData={eventData} />

                </Section>


            </VerticalContainer>

        </Box>
    );
}










const ConnexionSection = ({ props, eventData, promoMainData, setPromoMainData }) => {




    const isFree = eventData.prix === 0

    const { authState } = useContext(AuthContext)
    const [checked, setChecked] = useState(true)
    const [promoCode, setPromoCode] = useState('FALSE')

    const override = css`
        position: relative;
        right: 0px;
        bottom: 0px;
        z-index: 1;
        `;

    const handleChange = (event) => {
        setChecked(event.target.checked);
    }



    const { currentProcess, localState } = props

    let cardId = null


    if (authState.lemonwayData)
        cardId = authState.lemonwayData?.cards && authState.lemonwayData.cards.length > 0 && authState.lemonwayData.cards[0]


    const test = !isFree && ['antoine.delorme@pekoia.com', 'patrice.mouly@pekoia.com', 'laetitia_delorme@yahoo.fr'].includes(authState?.user?.attributes.email)


    const [promoTextField, setPromoTextField] = useState("")
    const [loading, setLoading] = useState(false)
    const [promoId, setPromoId] = useState(null)
    function handleClick() {

        // HANDLING CLICK OF PROMO BUTTON

        setLoading(true)

        setTimeout(() => {
            setLoading(false)
            setPromoTextField("")

            const found = authState?.codePromos
                .find((promo => (promo.category == 4)) && (promo => promo?.codeChain?.toUpperCase() === promoTextField))

            if (found) {

                setPromoId(found.id)
                setPromoCode(4)
                setPromoMainData({
                    promoCode: 4,
                    promoId: found.id
                })
            }




        }, 750)

    }

    const handleTextChange = (event) => {
        if (promoTextField.length < 17)
            setPromoTextField(event.target.value.toUpperCase());
    };

    return (<Box  width="100%" display="flex" height="100%" flexDirection="column" justifyContent="space-between" alignContent="center" alignItems='center'>

        {!isFree && <Typography style={{ color: "black", fontWeight: "bold", fontSize: "1rem", lineHeight: "1rem" }}>
            Paiement scuris
        </Typography>}
        {isFree && <Typography style={{ color: "black", fontWeight: "bold", fontSize: "1rem", lineHeight: "1rem" }}>
            Evnement gratuit
        </Typography>}
        {test && <Box
            height="100px"
            flexDirection="row"
            justifyContent="left"
            alignContent="center"
            sx={{ backgroundColor: yellow[100] }}
            padding='10px'>
            <Box>
                <Typography style={{ color: "black", fontSize: "0.7rem", lineHeight: "1rem", fontWeight: 600 }}>
                    Un code promo?
                </Typography>
            </Box>
            <Box height="50px" sx={{
                width: {
                    xs: 300, // theme.breakpoints.up('xs')
                    sm: 300, // theme.breakpoints.up('sm')
                    md: 400, // theme.breakpoints.up('md')
                    lg: 400, // theme.breakpoints.up('lg')
                    xl: 400, // theme.breakpoints.up('xl'
                },

            }}
                display="flex" flexDirection="row" alignItems="center" >

                <Box sx={{
                    width: {
                        xs: 200, // theme.breakpoints.up('xs')
                        sm: 200, // theme.breakpoints.up('sm')
                        md: 300, // theme.breakpoints.up('md')
                        lg: 300, // theme.breakpoints.up('lg')
                        xl: 300, // theme.breakpoints.up('xl'
                    }}} border={ '1px solid ' + grey[600] } >
                    <MiniValidationTextField
                        name="current-promo"
                        id="current-promo"
                        InputProps={{
                            autoFocus: false,
                            value: "test"

                        }}
                        InputLabelProps={{
                            shrink: true
                        }}
                        variant="outlined" value={promoTextField} onChange={handleTextChange} />
                </Box>
                
            <Box marginLeft="3px" width={100}>

                {!loading && <Button
                    size="small"
                    sx={{
                        borderColor: grey[300], color: grey[600], fontSize: 14, height: 40, width: 100,
                        "&:hover": {
                            borderColor: grey[500]
                        },
                    }}

                    onClick={handleClick}
                    loadingIndicator="Loading"
                    variant="outlined"
                >
                    <span>Valider</span>
                </Button>}
                {loading && <Box sx={{ display: 'flex', border: '1px solid ' + grey[400], height: 40, width: 100, borderRadius: 1, display: "flex", flexDirection: "row", alignItems: "center", justifyContent: "center" }} onClick={handleClick}>
                    <CircularProgress size='1.3rem' sx={{ color: grey[500] }} />

                </Box>}
            </Box>
        </Box>

        </Box>}

        <Box   display="flex" flexDirection="row" justifyContent="center" alignContent="center">
            {!isFree && <Typography style={{ color: "black", fontSize: "0.8rem", lineHeight: "1rem", padding: "10px" }}>
                Vous allez tre redirigs vers notre page de paiement scurise
            </Typography>}
            {isFree && <Typography style={{ color: "black", fontSize: "0.8rem", lineHeight: "1rem", padding: "10px" }}>
                Inscrivez vous  l'vnement
            </Typography>}


        </Box>
        <Box margin="20px 0 40px 0">
            <StyledButtonFilled onClick={() => currentProcess.workflowLogic.onClickMainButton({ ...props, promoCode: promoCode, promoId: promoId, authState: authState, eventData: eventData, cardId: checked ? cardId : null })}>
                {isFree && "Inscription"}
                {!isFree && "Effectuer le paiement"}

            </StyledButtonFilled>

        </Box>



        <Box marginTop="5px" display="flex" flexDirection="row" justifyContent="start" alignItems="center">
            {!isFree && cardId && <Box marginLeft="10px">

                <Checkbox
                    color="primary"
                    checked={checked}
                    onChange={handleChange}
                    inputProps={{ 'aria-label': 'primary checkbox' }}
                />
            </Box>}
            {!isFree && cardId && <Typography style={{ fontSize: '0.7rem', letterSpacing: "-0.15px", color: grey[600] }}>
                Utiliser la carte mmorise
            </Typography>}





        </Box>
{ (localState[SPINNER]) && <ClipLoader css={override} /> }

    </Box >)
}

const NoConnexionSection = ({ props, eventData }) => {

    const { setLocalState, localState } = props
    const theme = useTheme()
    const matches = useMediaQuery(theme.breakpoints.up('sm'))
    const { authState } = useContext(AuthContext)
    const [mounted, setMounted] = useState(false)


    useEffect(() => {
        if (!mounted) {
            setLocalState({ ...localState, EMAIL: authState.user.attributes?.email })
            setMounted(true)
        }


    }, [localState, mounted, setLocalState, authState])
    const [showTextField, setShowTextField] = useState(false)
    const classes = useStyles()

    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

    const handleClick = (event) => {
        setShowTextField(!showTextField)
        setLocalState({ ...localState, EMAIL: authState.user.attributes?.email })
    }


    const isReplayAvailable = eventData.isReplayAvailable

    let showMessage = `Vous recevrez un email de confirmation, avec le lien vous permettant de vous connecter le jour de lvnement,  l'adresse email suivante: `
    if (isReplayAvailable)
        showMessage = 'La confrence en replay sera disponible immdiatement aprs votre achat sur votre compte.'



    return (<Box marginTop="10px" padding="20px 0 20px 0" width="100%" display="flex" flexDirection={matches ? "row" : 'column'} justifyContent="center">
        <Box flex={1} padding="10px">
            <Typography style={{ color: "black", fontSize: "0.8rem", lineHeight: "1.3rem" }}>
                {showMessage}
            </Typography>
            {isReplayAvailable && <Box width="200px" marginTop="20px">
                <img
                    src="https://www.pekoia.com/static/replayHelp-76f23c4b068b429a36239dfc767871ce.jpg"
                    alt="aide">

                </img>
            </Box>}
            <Box display="flex" flexDirection="column">
                {!isReplayAvailable && <Box marginTop="15px" display="flex" flexDirection="row">
                    <Box>


                        {!showTextField && <Typography style={{ fontWeight: 'bold', fontSize: '1.1rem', color: grey[600] }}>

                            {authState.user.attributes?.email}
                        </Typography>}
                        <Box position="relative" right="13px">
                            {showTextField && <MiniValidationTextField
                                name="email"
                                id="changeEmailfield"
                                type="text"
                                label="e-mail"
                                autoComplete="section-blue email"
                                InputProps={{

                                    className: classes.input,
                                    autoFocus: true
                                }}

                                InputLabelProps={{

                                    shrink: true
                                }}
                                variant="outlined" value={localState[EMAIL]} onChange={onChange(EMAIL)} />}
                        </Box>
                    </Box>
                    <StyledMiniButton onClick={handleClick}>

                        {!showTextField && <EditIcon style={{ fontSize: "20px" }} />}
                        {showTextField && <CloseIcon style={{ fontSize: "20px" }} />}
                    </StyledMiniButton>

                </Box>}





            </Box>


        </Box>






    </Box>)
}

