import React, { useContext, useEffect, useState } from "react"
import Fade from '@mui/material/Fade';
import Box from '@mui/material/Box'
import Avatar from '@mui/material/Avatar'
import { GatsbyImage } from "gatsby-plugin-image"
import Typography from '@mui/material/Typography'
import useStyles, { BlackSubscription, BlackSubscriptionReplay, sxStyle } from './eventStyle'
import ShareIt from '../../../components/shareComponents'
import { API, graphqlOperation } from "@aws-amplify/api"
import { navigate } from "gatsby-link"
import { AuthContext } from '../../../context/general'


import getDate from "../../../helpers/dates"


import { grey, orange, yellow, red } from '@mui/material/colors';



const getEvent = `
query MyQuery {
    getEvent(id: "{{EVENT_ID}})") {
        id
        nbReserved
        attendingNb
        nbMax
    }
  }`

export const formatMapping = [{
    format: 'ZOOM',
    label: 'Zoom'
},
{
    format: 'MEET',
    label: 'Google meet'
},
{
    format: 'TEAMS',
    label: 'Microsoft teams'
}]


const SectionEvent = ({ data, authState, isPromo }) => {





    const classes = useStyles()

    if (!data)
        return null

    const pageEvent = data.pageEvent ? data.pageEvent : data.originalPageEvent


    const slug = pageEvent.fields.slug

    const url = data.site.siteMetadata.url + slug
    let pastEvent = false
    const date = data?.pageEvent?.date
    const secondsSinceEpoch = Math.round(Date.now() / 1000)

    if (date < secondsSinceEpoch)
        pastEvent = true











    return (<Box sx={sxStyle.sectionEvent}>
        <Box flex={1} display="flex" flexDirection="column">

            <SubAvatar data={data} />
            <SubEvent data={data} isPromo={isPromo} pastEvent={pastEvent} />
        </Box>
        <Box flex={1} padding="0 20px 0 20px" display='flex' flexDirection="column" alignContent="center" alignItems="center">
            <SubImage data={data} />
            <Box marginTop='15px' width="100%">
                <ShareIt title={pageEvent.title1} url={url} authState={authState} pastEvent={pastEvent} data={data} />
            </Box>
        </Box>

    </Box>)
}

export default SectionEvent

const SubAvatar = ({ data }) => {

    const pageEvent = data.pageEvent ? data.pageEvent : data.originalPageEvent

    const classes = useStyles()




    if (!data)
        return null



    const formateur = pageEvent.children.find(child => child.nom)

    if (!formateur)
        return null




    let avatarGatsbyImageData = null
    if (formateur?.children.length)
        avatarGatsbyImageData = formateur.children.find(image => image.original?.src.includes('portrait'))?.gatsbyImageData


    return (<Box display="flex" flexDirection="row">
        <Box onClick={() => {
            navigate(formateur.fields.slug)
        }} display="flex" flexDirection="row" alignContent="center" justifyContent="center" style={{ cursor: 'pointer' }}>

            <Avatar style={{ zIndex: 1 }} sx={sxStyle.avatarContainer}>
                <GatsbyImage
                    image={avatarGatsbyImageData}
                    alt={pageEvent.title1 ? pageEvent.title1 : ''}
                    loading="lazy"
                    style={{ zIndex: 2 }}

                />
            </Avatar>
        </Box>
        <Box padding="10px" paddingTop="20px">
            <Typography variant="h3" sx={sxStyle.avatarText}>
                {formateur.prenom}{" "} {formateur.nom}
            </Typography>

            <Box display="flex" flexWrap="wrap" flexDirection="rox" alignContent="center" alignItems='center'>

                {formateur.hashList.split('|').map((hash, index) => <Hash key={index} hash={hash} />)}
            </Box>
        </Box>

    </Box>)
}



const Hash = ({ hash }) => {
    return (<Box margin="0 3px 0 3px" padding="5px 12px 5px 12px" borderRadius="15px" marginTop="8px" bgcolor={grey[300]}>
        <Typography variant="h2" style={{ fontSize: '0.6rem' }}>
            {hash}
        </Typography>

    </Box>)
}





const SubEvent = ({ data, isPromo, pastEvent }) => {

    const pageEvent = data.pageEvent ? data.pageEvent : data.originalPageEvent

    const [showMessage, setShowMessage] = useState(false)
    const [message, setMessage] = useState(null)

    const getFormatted = (duree) => {
        if (duree === 60)
            return '1h'
        if (duree === 120)
            return '2h'
        if (duree === 90)
            return '1h30'
        else
            return duree + 'mn'
    }

    useEffect(() => {

        if (showMessage) {
            const interval = setInterval(() => setShowMessage(false), 5000)

            return () => clearInterval(interval)

        }



    }, [showMessage])


    const replayMessage = pageEvent.prix === 0 ? 'Obtenir le replay' : 'Acheter le replay'

    const dureeString = pageEvent.duree ? getFormatted(pageEvent.duree) : getFormatted(60)
    const isReplayAvailable = pageEvent.replayStatus === 'AVAILABLE' ? true : false
    const buttonMessage = pageEvent.replayStatus === 'AVAILABLE' ? replayMessage : "S'inscrire"



    const classes = useStyles()
    const { authState, authActions } = useContext(AuthContext)

    const clickHandler = (isReplayAvailable) => () => {


        const txs = authState.transactions
        const alreadyBought = (txs.filter(tx => tx.id == pageEvent?.fields.eventId)).length > 0 ? true : false
        console.log('@CLICK')
        console.log(alreadyBought)

        if (!alreadyBought) {

            const lemonwayData = authState.lemonwayData
            let cardId = null
            cardId = lemonwayData.cards && lemonwayData.cards.length && lemonwayData.cards[0]


            const query = getEvent.replace('{{EVENT_ID}})', pageEvent.fields.eventId)



            API.graphql(graphqlOperation(query))
                .then(({ data: APIdata }) => {
                    const eventData = APIdata.getEvent








                    if (isReplayAvailable || (eventData.nbMax - eventData.attendingNb - eventData.nbReserved) > 0)
                        authActions.toggleSubscriptionModal({
                            title1: pageEvent.title1,
                            cardId: cardId,
                            refererData: {
                                clientId: authState.parsed?.clientId

                            },

                            isReplayAvailable: isReplayAvailable,
                            title2: pageEvent.title2,
                            date: getDate(pageEvent.date),
                            outil: pageEvent.outil,
                            eventId: pageEvent.fields.eventId,
                            dureeString: dureeString,
                            prix: pageEvent.prix,
                            image: pageEvent.children.find(child => child.gatsbyImageData)?.gatsbyImageData,
                            formateur: pageEvent.children.find(child => child.lemonwayId),
                            lemonwayId: pageEvent.children.find(child => child.lemonwayId)?.lemonwayId
                        })
                    else {
                        setMessage('Il ny a plus de place disponible pour cet vnement')
                        setTimeout(() => setMessage(null), 5000)

                    }


                })
                .catch(err => console.log(err))

        }
        else {
            setShowMessage(true)
        }






    }





    return (<Box sx={sxStyle.subEventContainer}>
        <Box width="100%">
            <Typography variant="h1" sx={sxStyle.eventMainTitle}>
                {pageEvent.title1}
            </Typography>
            <Typography style={{ fontSize: '2rem', fontWeight: 500 }}>
                {pageEvent.title2}
            </Typography>
        </Box>
        <Box marginTop="30px">
            <Typography style={{ fontWeight: 'bold', lineHeight: '1rem' }}>
                {getDate(pageEvent.date).date}
            </Typography>
            <Typography style={{ fontSize: '0.8rem' }}>
                {getDate(pageEvent.date).time.replace(':', 'h')}
            </Typography>
        </Box>
        <Box sw={sxStyle.detailsContainer} >
            <Box with="100%" display="flex" flexDirection="column" alignContent="start" alignItems="start">
                <Box with="100%" display="flex" flexDirection="row" alignContent="center" alignItems="center" justifyContent="start" >


                    <Typography sx={sxStyle.detailsTitle}>
                        Format:
                    </Typography>
                    <Typography sx={sxStyle.detailsText}>
                        {!isReplayAvailable ? "confrence  distance" : "replay"}
                    </Typography>
                </Box>
                {!isReplayAvailable && <Box with="100%" display="flex" flexDirection="row" alignContent="center" alignItems="center" justifyItems="start" >
                    <Typography sx={sxStyle.detailsTitle}>
                        Outil:
                    </Typography>
                    <Typography sx={sxStyle.detailsText}>
                        {formatMapping.find(({ format }) => pageEvent.outil === format).label}
                    </Typography>

                </Box>}




            </Box>
            <Box display="flex" flexDirection="row" alignContent="center" alignItems="center">
                <Typography sx={sxStyle.detailsTitle}>
                    Dure:
                </Typography>
                <Typography sx={sxStyle.detailsText}>
                    {dureeString}
                </Typography>
            </Box>
            {!isReplayAvailable && <Box display="flex" flexDirection="row" alignContent="center" alignItems="center">


                <Typography sx={sxStyle.detailsTitle}>
                    Capacits:
                </Typography>
                <Typography sx={sxStyle.detailsText}>
                    {pageEvent.nbMax}{" "} personnes
                </Typography>
            </Box>}

        </Box>
        {!pastEvent && pageEvent.prix === 0 && <Box display="flex" flexDirection="row" marginTop="40px">
            <Typography style={{ fontSize: '1.8rem', fontWeight: "bold" }}>
                GRATUIT
            </Typography>
        </Box>}
        {(!pastEvent || isReplayAvailable) && pageEvent.prix > 0 && <Box display="flex" flexDirection="row" marginTop="40px">


            {isPromo && <Typography style={{ fontSize: '1.8rem', textDecoration: 'line-through', color: grey[400] }}>
                {(pageEvent.prix).toFixed(2)} 
            </Typography>}
            {isPromo && <Typography style={{ fontSize: '1.8rem', fontWeight: "bold", marginLeft: "10px" }} >
                {(pageEvent.prix - 2.5).toFixed(2)} 
            </Typography>}
            {!isPromo && <Typography style={{ fontSize: '1.8rem', fontWeight: "bold" }}>
                {pageEvent.prix.toFixed(2)} 
            </Typography>}
            <Typography sx={sxStyle.ttc}>
                TTC
            </Typography>
            {isPromo && <Box height="20px" padding="2px 7px 2px 7px" bgcolor={orange[700]} color="white" borderRadius="9px" marginRight="10px" display="flex" flexDirection="row" alignItems="center" alignContent="center" >
                <Typography style={{ fontSize: '0.6rem' }}>
                    {"parranaige"}
                </Typography>

            </Box>}
        </Box>}

        {(!pastEvent || isReplayAvailable) && <Box marginTop="20px" marginBottom='60px'>
            <BlackSubscriptionReplay onClick={clickHandler(isReplayAvailable)}>
                {buttonMessage}
            </BlackSubscriptionReplay>

        </Box>}
        <Box height="25px">
            <Typography style={{ color: "red" }}>
                <Fade in={showMessage}><Box padding='30px'>Vous tes dj inscrit  cette confrence.</Box></Fade>
                {message}
            </Typography>

        </Box>

    </Box>)
}

const SubImage = ({ data }) => {
    const pageEvent = data.pageEvent ? data.pageEvent : data.originalPageEvent


    const classes = useStyles()



    const children = pageEvent.children
        .filter(child => child.gatsbyImageData)
        .filter(child => child.original.src.includes('-800-'))
        .filter(child => !child.original.src.includes('-500-'))
        .filter(child => !child.original.src.includes('-300-'))
        .filter(child => !child.original.src.includes('blur'))




    let gatsbyImageData = null
    if (pageEvent.children.length)
        gatsbyImageData = children.find(child => (child.gatsbyImageData && child.original.src.includes('static/L')))?.gatsbyImageData




    return (
        <Box sx={sxStyle.mainImage} >


            <GatsbyImage
                image={gatsbyImageData}
                alt={pageEvent.title1 ? pageEvent.title1 : ''}
                loading="lazy"
                style={{ zIndex: 2, objectFit: 'cover', width: '100%', height: '100%', position: 'absolute', top: 0, left: 0 }}


            />
        </Box>

    )
}