
import React from 'react'

import Box from '@mui/material/Box'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';

import Typography from '@mui/material/Typography'

import Divider from '@mui/material/Divider'
import { RecapButton, useStyles } from '../../../style/modalsStyle'

import { GatsbyImage } from "gatsby-plugin-image"

import { NAME } from '../../../subscriptionModal/general'
import { formatMapping } from '../../../../templates/components/events/sectionEvent'



import { grey } from '@mui/material/colors'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';






const RecapSection = ({ props, eventData, closeHandler, toplabel, promoMainData }) => {

    const isValid = (promoMainData?.promoCode == 4)


    const [expand, setExpand] = React.useState(false)
    const classes = useStyles()


    if (!props)
        return null

    const { localState } = props

    const isLogin = localState[NAME] === 'LOGIN'
    const remiseConferencier = (promoMainData?.promoCode == 4) ? Math.round(eventData?.prix * 0.15*100) / 100 : 0

    const quickTruncate =(myText, NbChar)  =>{

       
        if (myText == null)
            return ""
        if (myText.length > NbChar)
            return myText.slice(0,NbChar)+' ...'
        return myText

    }


    const ContentSubscription = ({ toplabel }) => {
        return (<Box width="100%" height="40px" display="flex" flexDirection="row" justifyItems="stretch" justifyContent="start" alignContent="center" alignItems="center">
            <Box position="relative" bottom="5px" left="25px" onClick={() => closeHandler()} zIndex={50} >
                <ArrowBackIosIcon style={{ color: "white", fontSize: '20px', fontWeight: 300 }} />
            </Box>


            <Box position="relative" bottom="15px" width="100%" height="100%" display="flex" flexDirection="row" alignItems="center" justifyContent="center">


                <Typography variant="h4" style={{ fontSize: '0.9rem', color: 'white', fontWeight: 600 }}>
                    {quickTruncate(eventData?.title1, 35)}
                </Typography>

            </Box>
        </Box >)
    }


    const ExpandSubscription = ({ closeHandler }) => {
        return (
            <Box display="flex" zIndex={30} flexDirection="column" position="absolute" top="40px" width="calc(100%)" bgcolor="#191919" >
                <RecapButton onClick={() => setExpand(!expand)}>




                    {!expand && <Box display="flex" height="5px" flexDirection="row" justifyContent="center" position="relative" bottom="10px">

                        <Typography style={{ color: "white", fontSize: "0.6rem" }}>
                            dtails de l'venement
                        </Typography>
                        <KeyboardArrowDownIcon style={{ color: "white", position: "relative", left: '5px', top: "0px", fontSize: "1.2rem" }} />
                    </Box>}
                    {expand && <Box display="flex" height="5px" flexDirection="row" justifyContent="center" position="relative" bottom="10px">
                        <Typography style={{ color: "white", fontSize: "0.6rem" }}>
                            cacher les dtails
                        </Typography>
                        <KeyboardArrowUpIcon style={{ color: "white", position: "relative", left: '5px', top: "0px", fontSize: "1.2rem" }} />
                    </Box>}

                </RecapButton>
                <Box display={expand ? 'flex' : 'none'} flexDirection="column" alignItems="center" alignContent="end" >

                    <Box width="35%" padding="20px">
                        <GatsbyImage
                            image={eventData.image}
                            alt={eventData.title1 ? eventData.title1 : ''}
                            loading="lazy"
                            style={{ zIndex: 2, objectFit: 'cover', width: '100%' }}


                        />

                    </Box>
                    <Box display="flex" flexDirection="column">
                        <Typography style={{ fontSize: "0.8rem", color: grey[400] }}>
                            {"anim par "}{eventData?.formateur?.prenom}{" "} {eventData?.formateur?.nom}{eventData?.eventId && " sur "}{eventData?.eventId && formatMapping.find(elem => elem.format === eventData?.outil).label}

                        </Typography>
                        {eventData?.eventId && <Typography style={{ fontSize: "0.8rem", color: grey[400] }}>
                            dure: {eventData.dureeString}
                        </Typography>}

                    </Box>
                    <Box width="100%" style={{ zIndex: 999 }} marginTop="7px" marginBottom="20px" display="flex" flexDirection="row" justifyContent="center" alignContent="center">


                        <Box padding="10px" textAlign="center" height="50px" display="flex" width="300px" flexDirection="row" justifyContent="center" justifyItems="center">
                            <Box flex={1} display="flex" flexDirection="row" >
                                <Typography style={{ color: 'white', fontWeight: 'bold', textDecoration: isValid ? 'line-through' : 'none' }}>
                                    {eventData.date && eventData.prix + ""}
                                </Typography>
                                {isValid && <Typography style={{ color: 'white', fontWeight: 'bold' }}>
                                    {eventData.date && (eventData.prix - remiseConferencier) + ""}
                                </Typography>}

                            </Box>
                            <Box>
                                <Divider orientation="vertical" style={{ color: "yellow" }} />
                            </Box>
                            <Box flex={1} display="flex" flexDirection="column">
                                <Typography style={{ color: 'white', fontWeight: 'bold' }}>
                                    {eventData.date && eventData.date.dateShort.split('/').slice(0, 2).join('/')}
                                </Typography>
                                <Typography style={{ fontWeight: 400, color: 'white' }}>
                                    {eventData.date && eventData.date.weekDay.split(' ')[0].toUpperCase()}
                                </Typography>



                            </Box>
                            <Box>
                                <Divider orientation="vertical" style={{ color: "yellow" }} />
                            </Box>
                            <Box flex={1}>
                                <Typography style={{ color: 'white', fontWeight: 'bold' }}>
                                    {eventData.date && eventData.date.time}
                                </Typography>


                            </Box>


                        </Box>


                    </Box>

                </Box>

            </Box>
        )
    }

    const ContentLogin = ({ toplabel }) => {
        return (<Box width="100%" position="relative " display="flex" flexDirection="row" justifyItems="stretch" justifyContent="start" alignContent="center" alignItems="center">
            <Box position="absolute" left="25px" onClick={() => closeHandler()}>
                <ArrowBackIosIcon style={{ color: "white", fontSize: '30px', fontWeight: 300 }} />
            </Box>

            <Box width="100%" height="100%" display="flex" flexDirection="row" alignItems="center" justifyContent="center">


                <Typography variant="h4" style={{ color: 'white', fontWeight: 600 }}>
                    {toplabel}
                </Typography>

            </Box>
        </Box >)
    }

    return (<Box bgcolor="#191919" >
        <Box className={classes.topMenu}>
            {/* <Typography style={{color: "white"}}>
            {isLogin && "INSCRIPTION"}
            </Typography> */}

            {!isLogin && <ContentSubscription />}
            {!isLogin && <ExpandSubscription />}
            {isLogin && <ContentLogin toplabel={toplabel} />}
        </Box>




    </Box>)
}

export default RecapSection