import React from 'react'
import Box from '@mui/material/Box'

import { green, grey } from '@mui/material/colors'
import {  useStyles } from '../../../style/modalsStyle'

import ClearIcon from '@mui/icons-material/Clear'
import DoneIcon from '@mui/icons-material/Done'
import InputBase from '@mui/material/InputBase';

import Typography from '@mui/material/Typography'
import {testEmailLength, testEmailLower, testEmailUpper, testNoSpace} from '../../../helpers/general'




const tests = [
    {
        label: 'Aucun espace',
        testFunction: testNoSpace
    },
    {
        label: '8 caractres',
        testFunction: testEmailLength
    },
    {
        label: '1 minuscule',
        testFunction: testEmailLower
    },
    {
        label: '1 majuscule',
        testFunction: testEmailUpper
    }
]



const DisplayTests = ({ password }) => {
    return (
        <Box display="flex" flexDirection="row" flexWrap="wrap">
            {tests.map((test, index) => {
                return (<SingleTest key={index} label={test.label} testFunction={test.testFunction} password={password} />)
            })}
        </Box>)
}

const SingleTest = ({ password, label, testFunction }) => {


    const bgcolor = testFunction(password) ? green[100] : grey[100]
    const classes = useStyles()



    return (<Box  bgcolor={bgcolor} padding="2px 3px 2px 3px" borderRadius="10px" display="flex" flexDirection="row"  alignContent="center" alignItems="center" className={classes.simpleTestContainer}>
        <Box style={{ paddingRight: '5px', position:"relative", bottom:"4px"}}>
            {testFunction(password) ? <DoneIcon className={classes.simpleTestTypography} /> : <ClearIcon className={classes.simpleTestTypography} />}
        </Box>
        <Box>
            <Typography className={classes.simpleTestTypography}>
                {label}
            </Typography>

        </Box>



    </Box>)
}

const digitIndex = [0, 1, 2, 3, 4, 5]

export const DigitsInput = ({digit, setDigit}) => {

 

    const classes = useStyles();

    const handleKeyPress = (event, index) => {
        if (event.key === "Backspace") {
            if (digit[index] !== '') {
                const newDigit = [...digit]
                newDigit[index] = ''
                setDigit(newDigit)

            }
            else {
                const previousSibling = document.querySelector(
                    `input[name=ssn-${parseInt((index - 1) % 6)}]`
                )
                previousSibling && previousSibling.focus()

            }

        }
    }

    const handleInput = (event, index) => {

        const regex = /\d\d\d\d\d\d/
        const input = event.target.value

        if ((input.length === 6) && (input.search(regex) >= 0) && (testDigitIsEmpty(digit))) {

            const newDigit = [input[0], input[1], input[2], input[3], input[4], input[5]]
            setDigit(newDigit)

        }
        else {

            const nextSibling = document.querySelector(
                `input[name=ssn-${parseInt((index + 1) % 6)}]`
            )





            if (event.target.value?.length < 2 && (parseInt(event.target.value) || parseInt(event.target.value) === 0)) {
                const newDigit = [...digit]
                newDigit[index] = event.target.value
                setDigit(newDigit)
                if (event.target.value !== '' && nextSibling)
                    nextSibling.focus()
            }
        }
    }


    return (
        <Box display="flex" flexDirection="row" justifyContent="space-between">
            {digitIndex.map(index => {
                return (<InputBase
                  
                    autoFocus={(index === 0)}
                    key={index}
                    name={"ssn-" + index}
                    type="tel"
                    className={classes.inputDigit}
                    label="ThemeProvider"
                    value={digit[index]}
                    onKeyDown={(event) => handleKeyPress(event, index)}
                    onChange={(event) => handleInput(event, index)} />)

            })}

        </Box>
    )
}



const testDigitIsEmpty = (digit) => {
    if (!digit)
        return false
    let result = true
    digit.forEach(
        (digit) => {
            if (digit !== '')
                result = false
        }
    )
    return (result)



}

export default DisplayTests