
import React, { useContext } from 'react'
import { useState } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import { useStyles, NewValidationTextField } from '../../style/modalsStyle'
import { testEmail } from '../../../helpers/useful'

import Hidden from '@mui/material/Hidden'
import Checkbox from '@mui/material/Checkbox'
import { grey } from '@mui/material/colors'

import { AuthContext } from '../../../context/general'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import FrontOfficeTheme from '../../../style/globalTheme'


import { StyledButtonFilled3, StyledButtonFilled3Inactive, StyledButtonFilled4 } from '../../style/modalsStyle'
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { EMAIL } from '../../loginModal/logic'

import BannerLeft from './components/bannerLeft'
import TitleSection from './components/titleSection'
import Section from './components/section'
import RecapSection from './components/recapSection'
import VerticalContainer from './components/verticalContainer'



import {
    useWindowSize,

} from '@react-hook/window-size'

import { initialState } from '../../subscriptionModal/general'





const SubscriptionHomePage = (props) => {


    const { currentProcess, setLocalState } = props

    const closeHandler = () => {


        console.log('@CLOSE HANDLER')
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <StyledEngineProvider injectFirst>
                <ThemeProvider theme={FrontOfficeTheme}>
                    <MainContainer props={props} closeHandler={closeHandler}/>
                </ThemeProvider>
            </StyledEngineProvider>
        </ModalContainer>
    );

}

export default SubscriptionHomePage

const MainContainer = ({ props, closeHandler}) => {

    const { authState } = useContext(AuthContext)

    const eventData = authState.showSubscriptionModal



    const [ width , height] = useWindowSize()
    void height
    void width


    return (
        <Box display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="100%">


            <Hidden mdDown>

                <VerticalContainer width="200px" margin="0 20px 0 0px">

                    <Section RECAP_LARGE height="100%">
                        <BannerLeft props={props} eventData={eventData} />
                    </Section>


                </VerticalContainer >
            </Hidden>
            <VerticalContainer >

                <Hidden mdUp>
                    <Section RECAP flex={1}>
                        < RecapSection props={props} eventData={eventData} />

                    </Section>

                </Hidden>

                <Section flex={1}>
                    < TitleSection />

                </Section>
                <Section CONNECTION flex={1.5}>
                    <ConnexionSection CONNECTION props={props} />
                </Section>

                <Section CONNECTION flex={1}>
                    <NoConnexionSection props={props} />
                </Section>




            </VerticalContainer>

        </Box>
    );
}




const ConnexionSection = ({ props }) => {


    const classes = useStyles()


    const { currentProcess } = props

    return (<Box className={classes.withNoAccountConnexion}>
        <Box>
            <Typography variant= "h4"  style={{  fontWeight: "bold" }}>
                J'ai un compte
            </Typography>

        </Box>

        <Box>
            <StyledButtonFilled4 onClick={() => currentProcess.workflowLogic.onClickSecondButton(props)}>
                <Typography className={classes.buttonTypography}>
                Connexion
                </Typography>
                
        </StyledButtonFilled4>

        </Box>

    </Box>)
}

const NoConnexionSection = ({ props }) => {

    const { currentProcess, localState, setLocalState } = props




    const theme = useTheme()
    const matches = useMediaQuery(theme.breakpoints.up('sm'))
    void matches
    const classes = useStyles()

    const handlerOnChange = (event) => {
        setLocalState({ ...localState, EMAIL: event.target.value })
    }

    const [checked, setChecked] = useState(false)

    const handleChange = (event) => {
        setChecked(event.target.checked);
    }


    return (
        <Box padding="20px" display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="93%">


            <Box display="flex" flexDirection="column" justifyContent="space-between">
                <Typography style={{ color: "black", fontWeight: "bold", fontSize: "0.9rem", lineHeight: "1rem" }}>
                    Recevoir mes e-billets par e-mail
             </Typography>

                <Box marginTop="5px">
                    <Typography style={{ color: `${grey[500]}`, fontSize: "0.8rem", lineHeight: "1rem" }}>
                        Renseignez un mail pour la rception de vos e-billets :
            </Typography>

                </Box>

                <form className={classes.root} noValidate autoComplete="on" onSubmit={(event) => {
                    event.preventDefault()
                    if (testEmail(localState[EMAIL]) && checked)
                        currentProcess.workflowLogic.onClickMainButton(props)
                }
                }>
                    <Box>
                        <NewValidationTextField

                            name="email"
                            id="emailfield2"
                            fullWidth
                            type="text"
                            error={localState[EMAIL] !== '' && !testEmail(localState[EMAIL])}
                            autoComplete="section-red email"
                            InputProps={{
                                className: classes.input
                            }}
                            InputLabelProps={{
                                shrink: true
                            }}
                            variant="outlined" value={localState[EMAIL]} onChange={handlerOnChange} />
                    </Box>

                    <Box marginTop="10px" padding="20px" display="flex" flexDirection="row" alignItems="center">
                        <Checkbox
                            color="primary"
                            checked={checked}
                            onChange={handleChange}
                            inputProps={{ 'aria-label': 'primary checkbox' }}
                        />

                        <Typography style={{ letterSpacing: '-0.15px', fontSize: "0.7rem", lineHeight: "1rem" }}>
                            Jaccepte les CGV de Pekoia
                    </Typography>

                        {(testEmail(localState[EMAIL]) && checked) ?
                            <StyledButtonFilled3
                                type="submit">
                                <Typography >
                                    Continuer et payer
                      </Typography>

                            </StyledButtonFilled3> :
                            <StyledButtonFilled3Inactive
                                type="submit">
                                <Typography>
                                    Continuer et payer
                      </Typography>

                            </StyledButtonFilled3Inactive>
                        }
                    </Box>


                </form>

            </Box>

        </Box>





    )

}
