

export const EMAIL = 'EMAIL'
export const CONSENT = 'CONSENT'
export const PASSWORD = 'PASSWORD'
export const DIGITS = 'DIGITS'
export const SPINNER = 'SPINNER'
export const STAGE_SUBSCRIPTION = 'STAGE_SUBSCRIPTION'
export const ERROR_MESSAGE = 'ERROR_MESSAGE'
export const COUNT = 'COUNT'
export const NAME = 'NAME'
export const MODALNAME = 'SUBSCRIPTION'
export const PAGE_HOMEPAGE = 'PAGE_HOMEPAGE'
const PAGE_LOGIN = 'PAGE_LOGIN'

const localStateVariables = [SPINNER, NAME, EMAIL, PASSWORD, DIGITS, STAGE_SUBSCRIPTION]

const nullValuesMap = [{
    name: NAME,
    value: MODALNAME
},{
    name: STAGE_SUBSCRIPTION,
    value: PAGE_LOGIN
},
{
    name: EMAIL,
    value: ''
},
{
    name: PASSWORD,
    value: ''
},
{
    name: COUNT,
    value: 0
},
{
    name: SPINNER,
    value: false
},
{
    name: DIGITS,
    value: ['', '', '', '', '', '']
}]

const nullValue = (name) => {
    return (
        nullValuesMap.find(variable => variable.name === name).value
    )
}


const tempState = (authState) => {
    const state = {}


    localStateVariables.forEach(name => { state[name] = nullValue(name) })

   


    if (authState.user?.id)
        state[STAGE_SUBSCRIPTION] = state[STAGE_SUBSCRIPTION] + 'A'
    else
        state[STAGE_SUBSCRIPTION] = state[STAGE_SUBSCRIPTION] + 'A'


    return (state)
}


export const initialState = (authState) => tempState(authState)


