export const remises = [{
    cat: 'INV',
    amount: '2.5'
  }]


  export const getReduction = (parsed, authState) =>{


    
    const hasNeverBought = authState?.hasNeverBought || (authState && !authState.user.id)
   


      if (!parsed)
        return 0

    const remise = remises.find(remises => remises.cat ==parsed?.cat )
    if (remise && hasNeverBought)
        return remise.amount
    return 0

  }