

import React, { useContext, useState } from 'react'
import ModalContainer from '../helpers/modalContainer'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import BannerLeftLogin from './components/bannerLeftLogin'

import { grey } from '@mui/material/colors'

import Hidden from '@mui/material/Hidden'

import { testEmail } from '../../../helpers/useful'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';
import Checkbox from '@mui/material/Checkbox'
import FrontOfficeTheme from '../../../style/globalTheme'



import { useStyles, StyledButton, StyledButtonFilled, NewValidationTextField, StyledUnderlinedButton } from '../../style/modalsStyle'

import { EMAIL, PASSWORD, NAME, ERROR_MESSAGE, STAGE_SUBSCRIPTION, CONSENT } from '../../subscriptionModal/general'


import BannerLeft from './components/bannerLeft'
import { AuthContext } from '../../../context/general'
import VerticalContainer from './components/verticalContainer'

import { testEmailLength, testEmailLower, testEmailUpper, testNoSpace } from '../../helpers/general'

import Section from './components/section'
import DisplayTests from './components/displayTests'
import RecapSection from './components/recapSection'




import {
    useWindowSize,

} from '@react-hook/window-size'

import { initialState } from '../../subscriptionModal/general'







const SubscriptionHomePage = (props) => {




    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }


    return (
        <ModalContainer variant='SUBSCRIPTION_MODAL' closeHandler={closeHandler}>
            <StyledEngineProvider injectFirst>
                <ThemeProvider theme={FrontOfficeTheme}>
                    <MainContainer props={props} />
                </ThemeProvider>
            </StyledEngineProvider>
        </ModalContainer>
    );

}

export default SubscriptionHomePage

const MainContainer = ({ props }) => {

    const { localState } = props

    const [width, height] = useWindowSize()
    void height
    void width

    const { authState, authActions } = useContext(AuthContext)



    const eventData = authState.showSubscriptionModal


    return (
        <Box display="flex" bgcolor="white" flexDirection="row" justifyContent="center" alignItems="stretch" width="100%" height="100%">
            <Hidden mdDown>

                <VerticalContainer width="200px" margin="0 20px 0 0px">
                    

                    <Section RECAP_LARGE bgcolor="black" height="100%">
                        {localState[NAME] === 'LOGIN' && <BannerLeftLogin props={props} />}
                        {localState[NAME] === 'SUBSCRIPTION' && <BannerLeft props={props} eventData={eventData} />}
                    </Section>


                </VerticalContainer >
            </Hidden>

            <VerticalContainer  >
                

                <Hidden mdUp>
                    <Section RECAP >
                        < RecapSection toplabel="Nouveau compte" props={props} eventData={eventData} />
                    </Section>

                </Hidden>

                <Section bgcolor="white" flex={50}>
                    < LoginContainer props={props} />
                </Section>




            </VerticalContainer>

        </Box>
    );
}






const LoginContainer = ({ props }) => {

    const { currentProcess, localState, setLocalState } = props
    const { authState, authActions } = useContext(AuthContext)



    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

    const [checked, setChecked] = useState(true)

    const handleChange = () => {
        setChecked(!checked)
        setLocalState({ ...localState, [CONSENT]: !checked })
    }



    const classes = useStyles()
    return (
        <Box className={classes.sectionContainer} justifyContent="space-around">


            <Box borderRadius="10px" bgcolor="white" >

                <form className={classes.root} noValidate autoComplete="on" onSubmit={(event) => {
                    event.preventDefault()
                    if (testEmail(localState[EMAIL])) {


                        const password = localState[PASSWORD]
                        if ((testEmailLength(password)) && (testEmailLower(password)) && (testEmailUpper(password)) && (testNoSpace(password)))
                            currentProcess.workflowLogic.onClickMainButton({ ...props, authActions: authActions })
                        else
                            setLocalState({ ...localState, [ERROR_MESSAGE]: 'format de mot de passe invalide' })
                    }
                    else
                        setLocalState({ ...localState, [ERROR_MESSAGE]: "format d'adresse email invalide" })
                }
                }>
                    <Box  padding="0 20px 0 20px" alignContent="center" display="flex" width="100%" flexDirection="column" alignItems='center' >

                        <Box width="100%" >

                            <NewValidationTextField
                                name="email"
                                id="email"
                                type="text"
                                fullWidth
                                label="e-mail"
                                autoComplete="email"
                                InputProps={{

                                    className: classes.input,
                                    autoFocus: localState[STAGE_SUBSCRIPTION] === 'PAGE_LOGIN' ? true : false
                                }}

                                InputLabelProps={{
                                    shrink: true
                                }}
                                variant="outlined" value={localState[EMAIL]} onChange={onChange(EMAIL)} />



                        </Box>




                        <Box marginTop="10px" width="100%" >


                            <NewValidationTextField

                                name="password"
                                id="password"
                                type="password"
                                fullWidth
                                label="mot de passe"
                                autoComplete="new-password"
                                InputProps={{
                                    className: classes.input,
                                    autoFocus: localState[STAGE_SUBSCRIPTION] === 'STAGE_0B' ? true : false
                                }}
                                InputLabelProps={{
                                    shrink: true
                                }}
                                variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />
                        </Box>
                        <Box marginTop="5px" padding="10px">
                            <DisplayTests password={localState[PASSWORD]} />
                        </Box>


                        <StyledButtonFilled
                            type="submit">
                            <Typography className={classes.buttonTypography}>
                                {"crer".toUpperCase()}
                            </Typography>
                            <Typography>

                            </Typography>

                        </StyledButtonFilled>

                    </Box>
                    <Box margin="0 20px 0 20px" textAlign="center" height="20px" >
                    <Typography style={{ color: 'red', fontSize: '0.8rem' }}>
                        {localState[ERROR_MESSAGE]}
                    </Typography>

                </Box>
                </form>
               
            </Box>



            <Box borderRadius="10px" bgcolor="white" className={classes.loginContainer} >
                <Box width="100%" textAlign="center" margin="10px">
                    <Typography variant="h4" style={{ fontWeight: "bold" }}>
                        Vous avez un compte ?
                    </Typography>
                </Box>
                <Box padding="0px 0px 0px 0px">
                    <StyledButton
                        onClick={() => currentProcess.workflowLogic.onClickSecondButton(props)}>
                        <Typography className={classes.buttonTypography}>
                            {"Connectez vous".toUpperCase()}
                        </Typography>

                    </StyledButton>
                </Box>
                <Box margin="15px" display="flex" height="20px" flexDirection="row" alignContent="center" alignItems="center">
                    <Box>
                        <Checkbox
                            color="primary"
                            size="small"
                            checked={checked}
                            onChange={handleChange}
                            inputProps={{ 'aria-label': 'primary checkbox' }}
                        />

                    </Box>

                    <Typography style={{ fontSize: '0.8rem', lineHeight: '0.7rem' }}>
                        Je souhaite m'abonner la newsletter de Pekoia
                    </Typography>

                </Box>
                <Box textAlign="center">
                    <Typography style={{ fontSize: '0.7rem', lineHeight: '0.9rem', color: grey[500] }}>
                        En vous inscrivant, vous acceptez nos <a style={{ color: grey[500], textDecoration: 'underline' }} href="https://www.pekoia.com/cgu" target="_blank">conditions gnrales d'utilisation</a> ainsi que notre <a style={{ color: grey[500], textDecoration: 'underline' }} href="https://www.pekoia.com/confidentialite" target="_blank">politique de protection des donnes.</a>
                    </Typography>
                </Box>


            </Box>
        </Box>
    );
}




