import React from 'react'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'


import { grey } from '@mui/material/colors'
import {  useStyles, StyledButton, StyledSimpleButton } from '../../style/modalsStyle'
import { DIGITS, initialState, STAGE_3A, STAGE_3C } from '../../loginModal/logic'

import { EMAIL, PASSWORD, STAGE, ERROR_MESSAGE } from '../../loginModal/logic'
import { DisplayError, CustomPasswordField } from '../../helpers/general'
import ModalContainer, { Section, DigitsInput, DisplayTests } from './modalContainer'
import { testPassDigits, testPassword} from '../../helpers/general'





const DigitInputPage = (props) => {

    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }

    return (
        <ModalContainer closeHandler={closeHandler}>
            <Section>
                <TopSectionContent props={props} />
            </Section>
            <Section>
                <MiddleSectionContent props={props} />
            </Section>
            <Section>
                <BottomSectionContent props={props} />
            </Section>
        </ModalContainer>)
}

export default DigitInputPage



const TopSectionContent = ({ props }) => {




    const { localState } = props

    const title = (localState[STAGE] === STAGE_3A) ? `Vrification de votre email` : `Nouveau mot de passe`
    const topMessage = (localState[STAGE] === STAGE_3A) ? `Renseignez le code  6 chiffres envoy  la boite mail de ${localState[EMAIL]}. Attendez quelques dizaines de secondes si vous ne l'avez pas encore reu` : `Vous avez reu un code pour rinitialiser votre mot de passe. Vrifiez vos spams, si vous ne le voyez pas`

    return (
        <Box height="100px" width="100%" display="flex" flexDirection="column" alignItems="stretch">

            <Box marginTop="20px">
                <Typography style={{ textAlign: "center", fontWeight: 'bold', fontSize: '1.5rem' }}>
                    {title}
                </Typography>
            </Box>
            <Box paddingTop="20px">
                <Typography style={{ textAlign: "center", letterSpacing: '-0.5px', lineHeight: '17px', color: grey[600] }}>
                    {topMessage}
                </Typography>

            </Box>

        </Box>

    )

}

const MiddleSectionContent = ({ props }) => {

    const { localState, setLocalState } = props

    const onChange = (category) => (event) => {

        setLocalState({ ...localState, [category]: event.target.value })
    }

    const showSecondTextArea = (localState[STAGE] === STAGE_3C) ? true : false

    const classes = useStyles()

    return (<Box height="200px" width="100%" display="flex" flexDirection="column" justifyContent="space-between" alignItems="stretch">
        <Box width="100%" display="flex" flexDirection="column" alignItems="stretch">


        </Box>
        <DigitsInput digit={localState[DIGITS]} setDigit={(x) => setLocalState({ ...localState, [DIGITS]: x })} />
        <Box marginTop="20px">
            <form className={classes.root} noValidate autoComplete="on">
                <Box marginBottom="15px">
                {showSecondTextArea && <CustomPasswordField id ="standard-password" variableName={PASSWORD} className={classes.input} localState={localState}  onChange={onChange}/>}
                </Box>
            </form>
            <Box marginTop="20px">
            {showSecondTextArea && <DisplayTests password={localState[PASSWORD]} />}
            </Box>

        </Box>
        <DisplayError message={localState[ERROR_MESSAGE]} />

    </Box>)
}

const BottomSectionContent = ({ props }) => {

    const { localState, setLocalState, currentProcess } = props
    
    const clickHandler = () =>{
        if (!testPassDigits(localState.DIGITS))
            setLocalState({ ...localState, [ERROR_MESSAGE]: 'Nombre de chiffres insuffisants' })
         else if ((localState === STAGE_3C) && !testPassword(localState[PASSWORD]))
            setLocalState({ ...localState, [ERROR_MESSAGE]: "Format de mot de passe incorrect"})
        else {

            // const callback = (currentProcess) =>{
            //     setLocalState(initialState)
            //     currentProcess.closeHandler()
            // }
           

            currentProcess.page.workflowLogic.onClickMainButton(props)
            

        }   
            
        
    }



    return (<Box height="100px" width="100%" display="flex" flexDirection="column" align="center" alignItems="stretch" justifyContent="center" >
        <StyledButton
            onClick={clickHandler}>
            <Typography>
                {"envoyer".toUpperCase()}
            </Typography>

        </StyledButton>
    
        <StyledSimpleButton
            onClick={()=>currentProcess.page.workflowLogic.onClickButton(props)}>
            <Typography style={{ fontSize: '0.8rem', textDecoration: 'none', }}>
                Obtenir un nouveau code
            </Typography>

        </StyledSimpleButton>

    </Box>)
}




