import React from 'react'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'



import { grey } from '@mui/material/colors'
import { ValidationTextField, useStyles, StyledButton, StyledButtonFilled4, StyledUnderlinedButton } from '../../style/modalsStyle'
import { initialState } from '../../loginModal/logic'

import { EMAIL, PASSWORD, STAGE, ERROR_MESSAGE, STAGE_0B } from '../../loginModal/logic'
import { DisplayError } from '../../helpers/general'
import { Section } from './modalContainer'
import ModalContainer from '../helpers/modalContainer'



const HomePage = (props) => {

    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }

    return (
        <ModalContainer closeHandler={closeHandler}>
            <Section>
                <TopSectionContent />
            </Section>
            <Section>
                <MiddleSectionContent props={props} />
            </Section>
            <Section>
                <BottomSectionContent props={props} />
            </Section>
        </ModalContainer>)
}

export default HomePage



const TopSectionContent = () => {
    return (
        <Box height="100px" width="100%" display="flex" flexDirection="column" alignItems="stretch">
            <Box marginTop="20px">
                <Typography style={{ textAlign: "center", letterSpacing: '-0.5px', lineHeight: '17px', color: grey[600] }}>
                    ola airbnd build item in lorem
                </Typography>
            </Box>

            <Typography style={{ textAlign: "center", letterSpacing: '-0.5px', color: grey[600] }}>
                psum moratum PEKOIA
                </Typography>
            <Box paddingTop="20px">
                <Typography style={{ textAlign: "center", fontWeight: 'bold', fontSize: '1.5rem' }}>
                    Se connecter
                    </Typography>
            </Box>
        </Box>

    )

}

const MiddleSectionContent = ({ props }) => {

    const { localState, setLocalState, currentProcess } = props

    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

    const showSecondTextAread = (localState[STAGE] === STAGE_0B) ? true : false

    const classes = useStyles()

    return (<Box height="250px" width="100%" display="flex" flexDirection="column" justifyContent="space-between" alignItems="stretch">
        <Box width="100%" display="flex" flexDirection="column" alignItems="stretch">
            <Box padding="10px">
                <Typography style={{ fontSize: '0.8rem', color: grey[700] }}>
                    Saisissez une adresse email
                </Typography>

            </Box>



            <form className={classes.root} noValidate autoComplete="on" onSubmit={(event) => {
                event.preventDefault()
                currentProcess.workflowLogic.onClickMainButton(props)
            }
            }>
                <Box marginBottom="15px">
                   
                        <ValidationTextField
                            name="email"
                            id="emailfield"
                            type="text"
                            autoComplete="section-blue email"
                            InputProps={{

                                className: classes.input,
                                autoFocus: localState[STAGE] === 'STAGE_0A' ? true : false
                            }}

                            InputLabelProps={{
                                shrink: true
                            }}
                            variant="outlined" value={localState[EMAIL]} onChange={onChange(EMAIL)} />



                </Box>




                    <ValidationTextField

                        name="current-password"
                        id="current-password"
                        type="password"
                        autoComplete="section-blue current-password"
                        InputProps={{
                            className: classes.input,
                            autoFocus: localState[STAGE] === 'STAGE_0B' ? true : false
                        }}
                        InputLabelProps={{
                            shrink: true
                        }}
                        variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />


                    <StyledUnderlinedButton
                        type="submit"
                    >
                        <Typography style={{ fontSize: "0.8rem" }}>
                            Mot de passe oubli
                    </Typography>
                    </StyledUnderlinedButton>

                    <Box marginTop="20px">
                        <StyledButtonFilled4
                            type="submit">
                            <Typography>
                                {(localState[STAGE] === STAGE_0B) ? "connexion".toUpperCase() : "suivant".toUpperCase()}
                            </Typography>

                        </StyledButtonFilled4>
                    </Box>
            </form>
                {showSecondTextAread && <Box textAlign="right">

                </Box>}
        </Box>

            <DisplayError message={localState[ERROR_MESSAGE]} />


        </Box>)
}

const BottomSectionContent = ({ props}) => {

    const { currentProcess} = props


    return (<Box height="100px" width="100%" display="flex" flexDirection="column" align="center" alignItems="stretch" justifyContent="center" >
            <Box padding='5px'>
                <Typography style={{ fontSize: '0.9rem', color: grey[700] }}>
                    Vous n'avez pas de compte ?
            </Typography>
            </Box>

            <StyledButton
                onClick={() => currentProcess.workflowLogic.onClickSecondButton(props)}>
                <Typography>
                    {"crer votre compte pekoia".toUpperCase()}
                </Typography>

            </StyledButton>


        </Box>)
}




