
import Typography from '@mui/material/Typography'
import  Box  from '@mui/material/Box';
import React from 'react';
import {  red } from '@mui/material/colors'
import {ValidationTextField} from '../style/modalsStyle'

export const testEmail = (email) => {

 
    if (!email)
        return (null)
    const split1 = email.split('@')
    const split2 = (split1.length > 1) ? split1[1].split('.') : null

    if (split2 && split2[1])
        return (true)
    else
        return (false)

}

export const testPassword = (password) =>{
    return (testNoSpace(password) && testEmailLength(password) && testEmailLower(password) && testEmailUpper(password))
}

export const testNoSpace = (email) =>{

    if (!email)
    return (false)

    if (email.includes(' '))
        return false
    return true
}

export const testEmailLength = (email) =>{

    if (!email)
    return (false)


    if (email.length > 7)
        return true
    return false
}



export const testEmailLower = (email) =>{

    if (!email)
    return (false)


    if  (/[a-z]/.test(email)) 
        return true
    return false
}

export const testEmailUpper = (email) =>{

    if (!email)
    return (false)


    if  (/[A-Z]/.test(email))
        return true
    return false
}

export const testPassDigits = (digits) => {

    
    if (!digits)
        return false
    let result = true
    digits.forEach(
        (digit) => {
            
            if (digit === '')
                result = false
        }
    )
    return (result)

}

export const DisplayError = ({message})=>{
    return(<Box  marginTop="20px">
        <Typography style={{color: red[600], textAlign: "center"}}>
            {message}
        </Typography>
    </Box>)
}

export const CustomTextField = ({id, className, localState, variableName, onChange}) =>{
    return(<ValidationTextField
        id={id}
        autoFocus
        InputProps={{
            className: className
        }}

        InputLabelProps={{
            shrink: true
        }}
        variant="outlined" value={localState[variableName]} onChange={onChange(variableName)} />
    )
}


export const CustomPasswordField = ({id, className, localState, variableName, onChange}) =>{
    return(<ValidationTextField
        id={id}
        autoFocus
        type='password'
        InputProps={{
            className: className
        }}

        InputLabelProps={{
            shrink: true
        }}
        variant="outlined" value={localState[variableName]} onChange={onChange(variableName)} />
    )
}
