import React from 'react'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'


import { indigo, grey } from '@mui/material/colors'
import {  ValidationTextField, useStyles, StyledButton } from '../../style/modalsStyle'
import { initialState } from '../../loginModal/logic'

import { EMAIL, PASSWORD, STAGE, ERROR_MESSAGE, STAGE_0B } from '../../loginModal/logic'
import { DisplayError } from '../../helpers/general'
import ModalContainer, {Section, DisplayTests} from './modalContainer'



const SaisieMotDePassePage = (props) => {

    const { currentProcess, setLocalState } = props

    const closeHandler = () => {
        setLocalState(initialState)
        currentProcess.closeHandler()
    }

    return (
        <ModalContainer closeHandler={closeHandler}>
            <Section>
                <TopSectionContent props={props} />
            </Section>
            <Section>
                <MiddleSectionContent props={props} />
            </Section>
            <Section>
                <BottomSectionContent props={props} />
            </Section>
        </ModalContainer>)
}

export default SaisieMotDePassePage








const TopSectionContent = ({ props }) => {

    const { localState } = props

    return (
        <Box height="150px" width="100%" display="flex" flexDirection="column" alignItems="stretch">
            <Box marginTop="20px">

           
            <Typography align="center" style={{ fontWeight: 'bold', fontSize: '1.5rem' }}>
                Choisissez un mot de passe
            </Typography>
            </Box>
            <Box marginTop="15px">
            <Typography style={{ lineHeight: '1rem', textAlign: 'center', fontSize: '1.3rem', color: indigo[600] }}>
                {localState[EMAIL]}
            </Typography>
            </Box>
            
        </Box>

    )

}

const MiddleSectionContent = ({ props }) => {

    const { localState, setLocalState } = props

    const onChange = (category) => (event) => {
        setLocalState({ ...localState, [category]: event.target.value })
    }

    const showSecondTextAread = (localState[STAGE] === STAGE_0B) ? true : false

    const classes = useStyles()

    return (<Box height="200px" width="100%" display="flex" flexDirection="column" justifyContent="space-between" alignItems="stretch">
        <Box width="100%" display="flex" flexDirection="column" alignItems="stretch">
            <Box marginBottom="5px">

           
            <Typography style={{ fontSize: '0.8rem', marginBottom: '5px', color: grey[700] }}>
                Nouveau mot de passe
            </Typography>
            </Box>
            <form className={classes.root} noValidate autoComplete="on">

                <ValidationTextField
                    id="standard-password"
                    type="password"
                    InputProps={{
                        className: classes.input
                    }}
                    InputLabelProps={{
                        shrink: true
                    }}
                    variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />

            </form>
            <Box marginTop="20px">
                <DisplayTests password={localState[PASSWORD]} />
            </Box>
            


        </Box>
        {showSecondTextAread && <ValidationTextField
            id="standard-password"
            type="password"
            InputProps={{
                className: classes.input
            }}
            InputLabelProps={{
                shrink: true
            }}
            variant="outlined" value={localState[PASSWORD]} onChange={onChange(PASSWORD)} />}
      
        <DisplayError message={localState[ERROR_MESSAGE]} />

    </Box>)
}

const BottomSectionContent = ({ props }) => {

    const { currentProcess} = props


    return (<Box height="200px" width="100%" display="flex" flexDirection="column" align="center" alignItems="stretch" justifyContent="center" >
        <StyledButton
            onClick={() => currentProcess.workflowLogic.onClickMainButton(props)}>
            <Typography>
                {"valider".toUpperCase()}
            </Typography>
          

        </StyledButton>
      

    </Box>)
}




